<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row>
              <el-form :model="order">
                <el-form-item label="订单号:" label-width="100px">
                  {{order.orderNo}}
                </el-form-item>
                <el-form-item label="创建时间:" label-width="100px">
                  {{order.createTimeStr}}
                </el-form-item>
                <el-form-item label="状态:" label-width="100px">
                  {{order.payStatusStr}}
                </el-form-item>
                <el-form-item v-if="order.payStatus > 1" label="支付时间:" label-width="100px" >
                  {{order.payTimeStr}}
                </el-form-item>
              </el-form>
            </el-row>
            <br/>


          <el-row>
            <el-button type="success" style="height: 80px;width: 80px;font-size: 15px" @click="openChoosePrinter(order)">打单</el-button>
            <el-button type="danger" style="height: 80px;width: 80px;font-size: 15px" @click="handleEditTable()">换桌</el-button>
            <el-button type="info" style="height: 80px;width: 80px;font-size: 15px" @click="sureOrder()" v-if="order.applyStatus == 1" >确单</el-button>
            <el-button type="warning" style="height: 80px;width: 80px;font-size: 15px" @click="payOrder()" v-if="order.applyStatus == 2 && order.payStatus == 1" >收款</el-button>
          </el-row>
          <br/>

          <el-row>
            <el-input  autosize style="font-size:25px;" placeholder="请输入菜品名或编号" v-model="keyword" @input="initGoodsData" class="input-with-select">
            </el-input>
          </el-row>
          <br/>

          <el-table
            :data="goodsData"
            style="width: 100%">
            <el-table-column
              label="菜品"
              prop="name"
            >
              <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                  <p>销售价: ￥{{ scope.row.goods.marker_price/100 }}</p>
                  <br/>
                  <p>市场价: ￥{{ scope.row.goods.sell_price/100 }}</p>
                  <br/>
                  <p><el-button type="primary" @click="openNoteDialog(scope.row)">备注:{{scope.row.note}}</el-button></p>
                  <br/>
                  <p><el-button type="primary" @click="overGoods(scope.row)">确定上菜</el-button></p>
                  <br/>
                  <div slot="reference" class="name-wrapper">
                    <el-button type="success" style="height: 70px;"v-if="scope.row.count">
                      <span>{{ scope.row.goods.name }}</span>
                      (No.{{scope.row.goods.number}})
                      <br>
                      <span>￥{{scope.row.goods.sell_price/100}},￥{{scope.row.count*scope.row.goods.sell_price/100}}</span>
                      <br>
                      <span v-if="scope.row.count>0 && scope.row.makingCount == 0">未制作</span>
                      <span v-if="scope.row.count>0 && scope.row.makingCount > 0 && scope.row.makingCount > scope.row.makedCount">制作中</span>
                      <span v-if="scope.row.count>0 && scope.row.makingCount == scope.row.makedCount && scope.row.makedCount  > 0 && scope.row.overCount < scope.row.count">上菜中</span>
                      <span v-if="scope.row.count>0 && scope.row.overCount >= scope.row.count">已上完</span>

                    </el-button>
                    <el-button type="info" style="height: 50px;" v-else>
                      <span>{{ scope.row.goods.name }}</span>
                      (No.{{scope.row.goods.number}})
                      <br>
                      <span>￥{{scope.row.goods.sell_price/100}},￥{{scope.row.count*scope.row.goods.sell_price/100}}</span>
                    </el-button>
                  </div>
                </el-popover>
              </template>

            </el-table-column>

            <el-table-column label="数量">
              <template slot="header" slot-scope="scope">
                <span>总价:￥{{totalPrice/100}}</span>
              </template>
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.count" @change="handleChange(scope.row)" :min="scope.row.makingCount" :max="1000" label="下单数量"></el-input-number>
              </template>
            </el-table-column>
          </el-table>

        </div>


      <el-dialog title="菜品备注" :visible.sync="noteDialogTableVisible" width="90%" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
        <p>
          <el-input  type="textarea" rows="8" placeholder="请输入对该菜的备注" v-model="chooseOrderGoods.note" ></el-input>
        </p>
        <br/>
        <p>
          <el-tag type="success" @click="addNoteTag('大辣')">大辣</el-tag>
          <el-tag type="success" @click="addNoteTag('中辣')">中辣</el-tag>
          <el-tag type="success" @click="addNoteTag('小辣')">小辣</el-tag>
          <el-tag type="success" @click="addNoteTag('少盐')">少盐</el-tag>
          <el-tag type="success" @click="addNoteTag('多盐')">多盐</el-tag>
          <el-tag type="success" @click="addNoteTag('少油')">少油</el-tag>
          <el-tag type="success" @click="addNoteTag('多油')">多油</el-tag>
          <el-tag type="success" @click="addNoteTag('打包')">打包</el-tag>
        </p>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="sureNoteDialog">确 定</el-button>
        </div>
      </el-dialog>


      <el-dialog title="订单支付" :visible.sync="payTypeDialog" width="90%" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
        <p v-if="paymentConfigs.length == 0" style="font-size: 30px">
          <span >请前往收银台付款</span>
        </p>
        <p v-else>
          <a :href="baseUrl+'/payment/pay/'+item.id+'?order_id='+order.id+'&token='+token" target="_blank" v-for="(item,key) in paymentConfigs" :key="key"><el-button type="success">{{item.name}}</el-button></a>
        </p>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="closePayPop()">关  闭</el-button>
        </div>
      </el-dialog>

      <el-dialog title="打印" :visible.sync="printerDialogTableVisible">
        <el-divider content-position="left">打印机</el-divider>
        <div v-if="printers.length > 0">
          <el-radio v-model="choosePrinter" :label="item" border v-for="(item,key) in printers" :key="key" @change="handlePrinterChange">{{item.name}}</el-radio>
        </div>
        <div v-else>
          暂无打印机
        </div>
        <el-divider content-position="left">打印模板</el-divider>
        <div v-if="choosePrinter != null && choosePrinter.printerDataModels != null">
          <el-radio v-model="choosePrinterDataModels" :label="item" border v-for="(item,key) in choosePrinter.printerDataModels" :key="key">{{item.name}}</el-radio>
        </div>
        <div v-else>
          暂无打印模板
        </div>
        <span slot="footer" class="dialog-footer">
                <el-button @click="printerDialogTableVisible = false">取 消</el-button>
                <el-button type="primary" @click="printOrder" v-if="choosePrinter != null && choosePrinterDataModels != null">确 定</el-button>
              </span>
      </el-dialog>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                uploadUrl:baseUrl+'/common/imageUpload',
                loading:false,
                table_id:0,
                order:{
                      id:0,
                      number:"",
                      createTimeStr:"",
                      payTime:"",
                      payTimeStr:""
                  },
              goodsData: [],
              totalPrice:0,
              keyword:"",

              chooseOrderGoods:{},
              noteDialogTableVisible:false,
              paymentConfigs:[],
              payTypeDialog:false,
              token:null,

              choosePrinter:{},
              choosePrinterDataModels:null,
              printers:[],
              printerDialogTableVisible:false,
            }
        },
        created(){
          var storage = window.localStorage;
          this.token = storage["access_token"];
          this.table_id = this.$route.query.table_id;
          this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store_user/orders/table/'+this.table_id, that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.order = res.data.order;
                    that.order.createTimeStr = this.dateTimeFormat(that.order.createTime);
                    if(that.order.payTime != null && that.order.payTime != ""){
                      that.order.payTimeStr = this.dateTimeFormat(that.order.payTime);
                    }

                    that.initGoodsData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            initGoodsData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store_user/orders/orderGoods', {order_id:that.order.id,keyword:this.keyword}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.goodsData = res.data.orderGoods;
                    that.totalPrice = 0;
                    that.goodsData.forEach(function(value,i){
                      that.totalPrice += (value.goods.sell_price * value.count);
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            statusFormat(row,column){
                return row.status == 2?"上架":"下架";
            },
            markerPriceFormat(row,column){
              return row.goods.marker_price/100;
            },
            sellPriceFormat(row,column){
              return row.goods.sell_price/100;
            },
            totalPriceFormat(row,column){
              return row.goods.sell_price/100;
            },
          dateTimeFormat(time) {
            var date=new Date(time);
            var year=date.getFullYear();
            /* 在日期格式中，月份是从0开始的，因此要加0
             * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
             * */
            var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
            var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
            var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
            var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
            var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
            // 拼接
            return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
            //return year+"-"+month+"-"+day;
          },
          openNoteDialog(row){
            if(row.note == null){
              row.note = "";
            }
            this.chooseOrderGoods = row;
            this.noteDialogTableVisible = true;
          },
          sureNoteDialog(){
            this.handleChange(this.chooseOrderGoods);
            this.noteDialogTableVisible = false
          },
          addNoteTag(value){
            this.chooseOrderGoods.note = this.chooseOrderGoods.note + " " + value;
          },
          overGoods(orderGoods){
            this.$confirm('确定菜品:'+orderGoods.goodsName+'已经上完?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              var that = this;
              that.$api.put('/store_user/orders/order_goods/'+orderGoods.id,null, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            }).catch(() => {
            });

          },
          handlePrinterChange(){
            this.choosePrinterDataModels = null;
          },
          openChoosePrinter(row){
            this.order = row;
            var that = this;
            that.$api.get('/store_user/printer', null, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  that.printers = res.data.printers;
                  this.printerDialogTableVisible = true;
                  this.choosePrinter = null;
                  this.choosePrinterDataModels = null;

                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });
              }
              this.loading = false;
            });
          },
          printOrder(){
            var that = this;
            that.$api.put('/store_user/printer/'+that.choosePrinter.id+'/order/'+this.order.id+'/type/'+this.choosePrinterDataModels.type,null, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  this.$message({
                    message: "已加入到打印队列了",
                    type: 'success'
                  });
                  that.printerDialogTableVisible = false;
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });
              }
              this.loading = false;
            });
          },

          sureOrder(){
            this.$confirm('确定当前订单有效吗?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              var that = this;
              that.$api.put('/store_user/orders/'+this.order.id+"/status",null, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: "已确认当前订单",
                      type: 'success'
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            }).catch(() => {
            });
          },

          payOrder(){
            this.loading = true;
            var that = this;
            that.$api.get('/store_user/payment_config/can_used',null, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  that.payTypeDialog = true;
                  that.paymentConfigs = res.data.paymentsConfigs;
                  console.log(response);
                }else{
                  //this.$toast.error(res.msg)
                }
              } else {
                //this.$toast.error('服务器维护中')
              }
              this.loading = false;
            });
          },
          goPay(row){
            var storage = window.localStorage;
            var token = storage["access_token"];
            location.href= this.baseUrl+"/payment/pay/"+row.id+"?order_id="+this.order.id+"&token="+token+"&t="+(new Date().getTime());
          },
            handleChange(row){
              var that = this;
              that.$api.post('/store_user/orders/'+this.order.id+'/goods/'+row.goods.id, {num:row.count,note:row.note}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    //计算总价
                    that.totalPrice = 0;
                    that.goodsData.forEach(function(value,i){
                       that.totalPrice += (value.goods.sell_price * value.count);
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                    this.initData();
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                  this.initData();
                }
                this.loading = false;
              });
            },
            handleEditTable(){
              var that = this;


              this.$prompt('请输入餐桌编号', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
               // inputErrorMessage: '邮箱格式不正确'
              }).then(({ value }) => {

                that.$api.put('/store_user/orders/'+this.order.id+"/table", {table_number:value}, response => {
                  if (response.status >= 200 && response.status < 300) {
                    var res = response.data;
                    if (res.code == 0) {
                        this.table_id = res.data.table.id;
                        that.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                      this.initData();
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                    this.initData();
                  }
                  this.loading = false;
                });

              }).catch(() => {

              });
            },
            closePayPop(){
              this.payTypeDialog = false;
              this.initData();
            },
            in_array(search,array){
              for(var i in array){
                if(array[i]==search){
                  return true;
                }
              }
              return false;
            },



        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



