<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row>
                <el-button type="primary" @click="handleEdit(0, '','add')">新增标签</el-button>
                <el-button type="primary" @click="getMainStoreTags()">从总部复制标签</el-button>
            </el-row>
            <br/>
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="标签名"
                prop="name">
              </el-table-column>
                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      @click="handleEdit(scope.$index, scope.row,'edit')">
                        编辑
                    </el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDeleteAlert( scope.row)">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>

            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
                <el-form :model="goods_tag">
                    <el-form-item label="标签名" label-width="100px">
                        <el-input v-model="goods_tag.name" auto-complete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="handleSave">确 定</el-button>
                </div>
            </el-dialog>

            <el-dialog :title="tag_dialog.title" :visible.sync="tag_dialog.dialogFormVisible" :close-on-click-modal="tag_dialog.close">
              <el-form :model="goods_tag">
                <el-form-item label="总部标签" label-width="100px">
                  <el-checkbox border  v-for="(item,index) in tagChecks" :key="index"  :label="item.name" v-model="item.check" border></el-checkbox>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="tag_dialog.dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleCopy()">确定复制</el-button>
              </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                goods_tag:{
                    id:0,
                    name:""
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"新增菜品标签",
                    close:false,
                },
                tag_dialog:{
                  dialogFormVisible:false,
                  title:"总部菜品标签",
                  close:false,
                },
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                },
              tableData: [],
              tagChecks:[],
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/goods_tag', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.goods_tags.content;
                    that.pages.total = res.data.goods_tags.totalElements;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            getMainStoreTags(){
              this.tagChecks = [];
              this.loading = true;
              var that = this;
              that.$api.get('/store/goods_tag/main_goods_tags', {page:1,pageSize:10000}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    var tags = res.data.goods_tags.content;
                    var len = tags.length;
                    if(len > 0){
                      for(var i = 0 ; i < len ; i ++){
                        var value = tags[i];
                        this.tagChecks.push({check:false,name:value.name,id:value.id});
                      }
                    }
                    this.tag_dialog.dialogFormVisible = true;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            handleEdit(index, row,action) {
                if(action == "edit"){
                    this.dialog.title = "修改菜品标签";
                    this.goods_tag.id = row.id;
                    this.goods_tag.name = row.name;
                    this.dialog.dialogFormVisible = true;
                }else if(action == "add"){
                    this.dialog.title = "修改菜品标签";
                    this.goods_tag.id = 0;
                    this.goods_tag.name = "";
                    this.dialog.dialogFormVisible = true;
                }
            },
            handleCopy(){

              this.loading = true;
              var goods_tag_ids = "";
              this.tagChecks.forEach(function(value,i){
                if(value.check == true){
                  goods_tag_ids += value['id']+"#";
                }
              });
              if(goods_tag_ids == ""){
                this.loading = false;
                this.$message({
                  message: "请选择要复制的标签",
                  type: 'warning'
                });
                return;
              }
              var that = this;
              that.$api.post('/store/goods_tag/copy', {tag_ids:goods_tag_ids}, response => {
                if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '复制成功',
                      type: 'success'
                    });
                    this.tag_dialog.dialogFormVisible = false;
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }

              });

              this.loading = false;
            },

            handleSave() {
                this.loading = true;
                var that = this;
                that.$api.post('/store/goods_tag', that.goods_tag, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },
            handleDeleteAlert(row) {
                this.$confirm('此操作将删除该菜品标签, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.handleDelete(row);
                }).catch(() => {

                });
            },
          handleDelete(row) {
            this.loading = true;
            var that = this;
            that.$api.delete('/store/goods_tag/'+row.id, null, response => {
              if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '删除成功',
                      type: 'success'
                    });
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                    this.loading = false;
                  }
              } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                this.loading = false;
              }

            });
            },
        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



