<template>
    <div class="fillcontain" style="background-color: white">
        <!-- 选择餐桌 -->
        <div class="table_container" v-show="chooseTableContainer">
            <el-row>
                <el-input autosize style="font-size:25px;" placeholder="请输入餐桌编号" v-model="pages.keyword" @input="initData" class="input-with-select">
                </el-input>
            </el-row>
            <br/>
            <div v-loading="loading">
                <el-button :type="item.btnType" @click="chooseTable(item)" v-for="(item,key) in tableData" :key="key" style="height: 100px;width: 100px;font-size: 15px;margin: 3px;text-align: center">
                  {{item.name}}
                  <br/>
                  <strong>
                  {{item.number}}
                  </strong>
                </el-button>
            </div>
          <br/>
          <br/>
          <br/>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>


          <el-dialog
            title="操作"
            :visible.sync="actionDialogVisible"
            width="90%"
            :before-close="actionDialogHandleClose">

            <el-button type="success" style="height: 80px;width: 80px;font-size: 15px" @click="initGoodsData">点餐</el-button>
            <el-button type="danger" style="height: 80px;width: 80px;font-size: 15px" @click="goOrderDetail">看单</el-button>
            <el-button type="primary" style="height: 80px;width: 80px;font-size: 15px" @click="changeTable">换桌</el-button>
            <br/><br/>
            <el-button type="info" style="height: 80px;width: 80px;font-size: 15px" @click="resetTable(2)">占用</el-button>
            <el-button type="primary" style="height: 80px;width: 80px;font-size: 15px" @click="resetTable(1)">翻台</el-button>
            <!--<el-button  style="height: 100px;width: 100px;font-size: 30px" @click="assembleTable">并桌</el-button>-->
            <el-button style="height: 80px;width: 80px;font-size: 15px" @click="actionDialogHandleClose">取消</el-button>
          </el-dialog>

        </div>
      <!-- 选择菜品 -->
      <div class="table_container" v-show="chooseGoodsContainer">
        <el-row>
          <el-input  autosize style="font-size:25px;" placeholder="请输入菜品名或编号" v-model="goodsPages.keyword" @input="initGoodsData" class="input-with-select">
          </el-input>
        </el-row>
        <br/>
        <div v-loading="loading">

          <el-table
            :data="goodsData"
            style="width: 100%">
            <el-table-column
              label="菜品"
              prop="name"
              style="width: 40%"
            >
              <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                  <p>销售价: ￥{{ scope.row.marker_price/100 }}</p>
                  <br/>
                  <p>市场价: ￥{{ scope.row.sell_price/100 }}</p>
                  <br/>
                  <p><el-button type="primary" @click="openNoteDialog(scope.row)">备注:{{scope.row.note}}</el-button></p>
                  <br/>
                  <div slot="reference" class="name-wrapper">
                    <el-button type="success" style="height: 60px;"v-if="scope.row.cart_num">
                      <span>{{ scope.row.name }}</span>
                      (No.{{scope.row.number}})
                      <br>
                      <span>￥{{scope.row.sell_price/100}},￥{{scope.row.cart_num*scope.row.sell_price/100}}</span>

                    </el-button>
                    <el-button type="info" style="height: 60px;" v-else>
                      <span>{{ scope.row.name }}</span>
                      (No.{{scope.row.number}})
                      <br>
                      <span>￥{{scope.row.sell_price/100}},￥{{scope.row.cart_num*scope.row.sell_price/100}}</span>

                    </el-button>
                  </div>
                </el-popover>
              </template>

            </el-table-column>

            <el-table-column label="数量"
                             style="width: 60%"
            >
              <template slot="header" slot-scope="scope">
                <span style="font-size: 15px">共:￥{{totalPrice/100}}</span>   <el-button type="danger" @click="openCreateOrderDialog">下单</el-button>
              </template>
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.cart_num" @change="handleChange(scope.row)" :min="0" :max="1000" label="下单数量"></el-input-number>
              </template>
            </el-table-column>
          </el-table>

        </div>


        <el-dialog title="菜品备注" :visible.sync="noteDialogTableVisible" width="90%" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
          <p>
            <el-input  type="textarea" rows="8" placeholder="请输入对该菜的备注" v-model="chooseCartGoods.note" ></el-input>
          </p>
          <br/>
          <p>
            <el-tag type="success" @click="addNoteTag('大辣')">大辣</el-tag>
            <el-tag type="success" @click="addNoteTag('中辣')">中辣</el-tag>
            <el-tag type="success" @click="addNoteTag('小辣')">小辣</el-tag>
            <el-tag type="success" @click="addNoteTag('少盐')">少盐</el-tag>
            <el-tag type="success" @click="addNoteTag('多盐')">多盐</el-tag>
            <el-tag type="success" @click="addNoteTag('少油')">少油</el-tag>
            <el-tag type="success" @click="addNoteTag('多油')">多油</el-tag>
            <el-tag type="success" @click="addNoteTag('打包')">打包</el-tag>
          </p>
          <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="sureNoteDialog">确 定</el-button>
          </div>
        </el-dialog>

        <!-- 创建订单 -->
        <el-dialog title="下单确认" :visible.sync="createOrderialogFormVisible" width="90%">
          <el-form :model="cart">
            <el-form-item label="就餐人数" label-width="100px">
                <el-input-number v-model="cart.people_num" :min="1" :max="10000" label="人数"></el-input-number>
            </el-form-item>
            <el-form-item label="下单备注" label-width="100px">
              <el-input v-model="cart.note" type="textarea" rows="4"></el-input>
            </el-form-item>

          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="createOrderialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="createOrder">确 定</el-button>
          </div>
        </el-dialog>

      </div>

    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                chooseTableContainer:true,
                chooseGoodsContainer:false,
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                    keyword:"",
                },
                goodsPages:{
                  offset: 0,
                  pageSize: 10,
                  page: 1,
                  total: 0,
                  keyword:"",
                },
              tableData: [],
              goodsData:[],
              cart:{
                  table_id:0,
                  note:"",
                  people_num:"",
              },
              chooseCartGoods:{},
              choseTable:{},
              actionDialogVisible: false,
              noteDialogTableVisible:false,
              createOrderialogFormVisible:false,
              totalPrice:0,
              action:0,//1.换桌，2并桌
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store_user/tables', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    var tableData = res.data.tables.content;

                    that.tableData = [];
                    tableData.forEach(function(value,i){
                      if(value.status == 1) {
                          value.btnType = "success";
                      }else{
                          value.btnType = "info";
                      }
                        that.tableData.push(value);

                    });

                    that.pages.total = res.data.tables.totalElements;

                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

          initGoodsData(){
            this.actionDialogVisible = false;
            this.chooseTableContainer = false;
            this.chooseGoodsContainer = true;
            this.loading = true;
            var that = this;
            that.$api.get('/store_user/cart', {table_id:this.choseTable.id,keyword:this.goodsPages.keyword}, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  this.goodsData = res.data.goods;
                  that.totalPrice = 0;
                  that.goodsData.forEach(function(value,i){
                    that.totalPrice += (value.sell_price * value.cart_num);
                  });
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });
              }
              this.loading = false;
            });
          },
          goOrderDetail(row){
            this.$router.push({path:'/storeUser/orderDetail',query: {table_id: this.choseTable.id}});
          },
          openNoteDialog(row){
              if(row.note == null){
                row.note = "";
              }
              this.chooseCartGoods = row;
              this.noteDialogTableVisible = true;
          },
          sureNoteDialog(){
              this.handleChange(this.chooseCartGoods);
              this.noteDialogTableVisible = false
          },
          addNoteTag(value){
            this.chooseCartGoods.note = this.chooseCartGoods.note + " " + value;
          },
          handleChange(row){
            var that = this;
            that.$api.post('/store_user/cart/table/'+this.cart.table_id+'/goods/'+row.id, {num:row.cart_num,note:row.note}, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  //计算总价
                  that.totalPrice = 0;
                  that.goodsData.forEach(function(value,i){
                    that.totalPrice += (value.sell_price * value.cart_num);
                  });
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });
              }
              this.loading = false;
            });
          },
          openCreateOrderDialog(){
            this.cart.note = "";
            this.createOrderialogFormVisible = true;
          },
          createOrder(){
            this.cart.table_id = this.choseTable.id;
            this.loading = true;
            var that = this;
            that.createOrderialogFormVisible = false

            that.$api.post('/store_user/orders/'+that.cart.table_id, that.cart, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  this.$message({
                    message: "下单成功",
                    type: 'success'
                  });
                  this.chooseTableContainer = true;
                  this.chooseGoodsContainer = false;;
                  this.initData();
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });
              }
              this.loading = false;
            });
          },
          resetTable(status) {
            var title = "翻台";
            var msg = "翻台将使该桌子重置为空闲";
            if(status == 2){
              msg = "占用将使该桌子置为使用中";
              title = "占用";
            }
            this.$confirm(msg+',是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              var that = this;
              this.loading = true;
              that.$api.put('/store_user/tables/'+this.choseTable.id, {status:status}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                      that.actionDialogVisible = false;
                      this.$message({
                        message: title+"成功",
                        type: 'success'
                      });
                      that.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            }).catch(() => {

            });
          },
          changeTable() {//换桌
            this.action = 1;
            this.actionDialogVisible = false;
            this.$message({
              message: "请选择要换到的桌子",
              type: 'success'
            });

          },
          assembleTable(){//并桌
            this.action = 2;
            this.actionDialogVisible = false;
            this.$message({
              message: "请选择要并的桌子",
              type: 'success'
            });

          },
            chooseTable(value){
                if(this.action == 1){//换桌事件

                  if(this.choseTable.id == value.id){
                    this.action = 0;
                    this.$message({
                      message: "取消换桌成功",
                      type: 'success'
                    });
                    if(this.choseTable.status == 1) {
                      this.choseTable.btnType = "success";
                    }else{
                      this.choseTable.btnType = "info";
                    }
                    return;
                  }

                  this.$confirm('确定要让'+this.choseTable.name+'桌换到'+value.name+'吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                  }).then(() => {
                    var that = this;
                    this.loading = true;
                    that.$api.put('/store_user/cart', {from_table_id:this.choseTable.id,to_table_id:value.id}, response => {
                      if (response.status >= 200 && response.status < 300) {
                        var res = response.data;
                        if (res.code == 0) {
                          that.actionDialogVisible = false;
                          this.$message({
                            message: "换桌成功",
                            type: 'success'
                          });

                          this.action = 0;

                          if(this.choseTable.status == 1) {
                            this.choseTable.btnType = "success";
                          }else{
                            this.choseTable.btnType = "info";
                          }

                          value.btnType = "primary";
                          this.choseTable = value;
                          this.cart.table_id = value.id;
                          this.initData();
                        }else{
                          this.$message({
                            message: res.msg,
                            type: 'warning'
                          });
                        }
                      } else {
                        this.$message({
                          message: "服务器维护中",
                          type: 'error'
                        });
                      }
                      this.loading = false;
                    });
                  }).catch(() => {
                    this.action = 0;
                  });
                }else if(this.action == 2){//并桌事件
                  if(this.choseTable.id == value.id){
                    this.action = 0;
                    this.$message({
                      message: "取消并桌成功",
                      type: 'success'
                    });
                    if(this.choseTable.status == 1) {
                      this.choseTable.btnType = "success";
                    }else{
                      this.choseTable.btnType = "info";
                    }
                    return;
                  }
                  this.$confirm('确定要让'+this.choseTable.name+'和'+value.name+'并桌吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                  }).then(() => {
                    var that = this;
                    this.loading = true;
                    that.$api.post('/store_user/tables/assemble_table', {from_table_id:this.choseTable.id,to_table_id:value.id}, response => {
                      if (response.status >= 200 && response.status < 300) {
                        var res = response.data;
                        if (res.code == 0) {
                          that.actionDialogVisible = false;
                          this.$message({
                            message: "并桌成功",
                            type: 'success'
                          });

                          this.action = 0;
                          this.initData();

                        }else{
                          this.$message({
                            message: res.msg,
                            type: 'warning'
                          });
                        }
                      } else {
                        this.$message({
                          message: "服务器维护中",
                          type: 'error'
                        });
                      }
                      this.loading = false;
                    });
                  }).catch(() => {
                    this.action = 0;
                  });

                }else{//点击事件
                  value.btnType = "primary";
                  this.choseTable = value;
                  this.cart.table_id = value.id;
                  this.actionDialogVisible = true;
                }
            },
            actionDialogHandleClose(){
              if(this.choseTable.status == 1) {
                this.choseTable.btnType = "success";
              }else{
                this.choseTable.btnType = "info";
              }
              this.actionDialogVisible = false;
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            statusFormat(row,column){
                return row.status == 2?"上架":"下架";
            },
            markerPriceFormat(row,column){
              return row.marker_price/100;
            },
            sellPriceFormat(row,column){
              return row.sell_price/100;
            },


            in_array(search,array){
              for(var i in array){
                if(array[i]==search){
                  return true;
                }
              }
              return false;
            },


            handleSave() {
                this.loading = true;
                var that = this;

                that.goods.pic_ids = "";
                for (let i = 0, len = this.picFiles.length; i < len; i++) {
                  that.goods.pic_ids += this.picFiles[i].id + "#";
                }
                that.goods.tag_ids = "";
                this.tagChecks.forEach(function(value,i){
                  if(value.check == true){
                    that.goods.tag_ids += value['id']+"#";
                  }
                });
              that.goods.sell_price = that.goods.sell_price*100;//转换为分
              that.goods.marker_price = that.goods.marker_price*100;//转换为分
                that.$api.post('/store/goods', that.goods, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },
            handleDeleteAlert(row) {
                this.$confirm('此操作将删除该菜品, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.handleDelete(row);
                }).catch(() => {

                });
            },
          handleDelete(row) {
            this.loading = true;
            var that = this;
            that.$api.delete('/store/goods/'+row.id, null, response => {
              if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '删除成功',
                      type: 'success'
                    });
                    this.loading = false;
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
              } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
              }

            });
            },
        },
    }
</script>



