<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="打印点"
                prop="name">
              </el-table-column>

              <el-table-column
                label="状态"
                :formatter="statusFormat">
              </el-table-column>

                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      @click="handleEdit(scope.$index, scope.row,'edit')">
                        配置
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>

            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
                <el-form :model="printerDataModel">

                    <el-form-item label="模板" label-width="80px">
                      <el-row>
                        <el-col :span="14"><el-input v-model="printerDataModel.printData" type="textarea" rows="50" placeholder="订单号: ###b### ###order_no###  ###/b###
###br###
桌号: ###table_number###
###br###

菜品            单价            数量             总计
###br###
###start_goods_content###
###goods_name###            ###goods_sell_price###            ###goods_count###            ###goods_total_price###
###br###
###end_goods_content###

###br###
总金额：###total_price###
###br###
###cut###"></el-input></el-col>
                        <el-col :span="8" style="margin-left: 15px">
                          <p>替换内容</p>
                          <p>###order_no### : 订单号</p>
                          <p>###out_no### : 订单序号</p>
                          <p>###total_price### : 订单总价</p>
                          <p>###real_price### : 订单支付价格</p>
                          <p>###table_number### : 餐桌编号</p>
                          <p>###note### : 订单备注</p>
                          <p>###goods_name### : 菜品名字</p>
                          <p>###goods_sell_price### : 菜品单价</p>
                          <p>###goods_count### : 菜品数量</p>
                          <p>###goods_count### : 菜品总价</p>
                          <p>###goods_count### : 菜品数量</p>
                          <p>###start_goods_content### : 菜品内容开始符</p>
                          <p>###end_goods_content### : 菜品内容结束符</p>
                          <p>###br### : 换行</p>
                          <p>###cut### : 切刀指令(主动切纸,仅限切刀打印机使用才有效果)</p>
                          <p>###cb###  ###/cb###: 居中放大</p>
                          <p>###b###  ###/b###: 放大一倍</p>
                          <p>###c###  ###/c###: 居中</p>
                          <p>###l###  ###/l###: 字体变高一倍</p>
                          <p>###w###  ###/w###: 字体变宽一倍</p>
                          <p>###right###  ###/right###: 右对齐</p>
                          <p>###bold###  ###/bold###: 加粗</p>
                        </el-col>
                      </el-row>
                    </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="handleSave">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                printerDataModel:{
                    id:0,
                    status:1,
                    printData:""
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"新增支付",
                    close:false,
                },
              tableData: [],
              params:[],
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/printer_data_model', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.printerDataModels;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            statusFormat(row,column){
              return (row.printData == null || row.printData == "")?"未配置":"已配置";
            },
            handleEdit(index, row,action) {
                    this.dialog.title = "配置:"+row.name;
                    this.printerDataModel.id = row.id;
                    this.printerDataModel.name = row.name;
                    this.printerDataModel.printData = row.printData;
                    // for(var key in row.paramsMap){
                    //   this.params.push({name:key,value:row.paramsMap[key]['value'],placeholder:row.paramsMap[key]['placeholder']});
                    // }

                    this.dialog.dialogFormVisible = true;
            },
            handleSave() {
                this.loading = true;
                var that = this;




                that.$api.put('/store/printer_data_model/'+that.printerDataModel.id, that.printerDataModel, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },
        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



