import Vue from 'vue'
import Router from 'vue-router'




Vue.use(Router)

//superAdmin
import superAdminIndex from '../pages/superAdmin/Full.vue';
import superAdminLogin from '../pages/superAdmin/Login.vue';
import superAdminMainStore from '../pages/superAdmin/MainStore.vue';
import superAdminSystemActive from '../pages/superAdmin/Active.vue';


//mainStore
import mainStoreIndex from '../pages/mainStore/Full.vue';
import mainStoreLogin from '../pages/mainStore/Login.vue';
import mainStoreStore from '../pages/mainStore/Store.vue';
import mainStoreGoodsTag from '../pages/mainStore/GoodsTag.vue';
import mainStoreGoods from '../pages/mainStore/Goods.vue';
import mainStorePaymentConfig from '../pages/mainStore/PaymentConfig.vue';
import mainStoreOauthConfig from '../pages/mainStore/OauthConfig.vue';
import mainStorePrinterDataModel from '../pages/mainStore/PrinterDataModel.vue';
import mainStoreOrder from '../pages/mainStore/Orders.vue';
import mainStoreOrderDetail from '../pages/mainStore/OrderDetail.vue';

import mainStorePageView from '../pages/mainStore/PageViewConfig.vue';
import mainStorePluginsStore from '../pages/mainStore/Plugins.vue';
import mainStoreChargeItem from '../pages/mainStore/ChargeItem.vue';

//store
import storeIndex from '../pages/store/Full.vue';
import storeLogin from '../pages/store/Login.vue';
import storeGoodsTag from '../pages/store/GoodsTag.vue';
import storeGoods from '../pages/store/Goods.vue';
import storePaymentConfig from '../pages/store/PaymentConfig.vue';
import storeOauthConfig from '../pages/store/OauthConfig.vue';
import storePrinterDataModel from '../pages/store/PrinterDataModel.vue';
import storePrinter from '../pages/store/Printer.vue';
import storeStoreUser from '../pages/store/StoreUser.vue';
import storeTable from '../pages/store/Tables.vue';
import storeOrder from '../pages/store/Orders.vue';
import storeSetting from '../pages/store/Setting.vue';
import storeOrderDetail from '../pages/store/OrderDetail.vue';
import storePageView from '../pages/store/PageViewConfig.vue';

import storePluginsStore from '../pages/store/Plugins.vue';
import storeCallTag from '../pages/store/CallTag.vue';

import storeCallLog from '../pages/store/CallLog.vue';
import storeChargeItem from '../pages/store/ChargeItem.vue';

import storeConsumer from '../pages/store/Consumer.vue';

import storeOrderFoodPluginsConfig from '../pages/store/OrderFoodPluginsConfig.vue';

//storeUser
import storeUserIndex from '../pages/storeUser/Full.vue';
import storeUserLogin from '../pages/storeUser/Login.vue';
import storeUserCart from '../pages/storeUser/Carts.vue';
import storeUserOrderDetail from '../pages/storeUser/OrderDetail.vue';
import storeUserOrderGoods from '../pages/storeUser/OrderGoods.vue';
import storeUserCallLog from '../pages/storeUser/CallLog.vue';

export default new Router({
  routes: [

    //superAdmin
    {path: '/superAdmin/login', name: '登录', component: superAdminLogin},
    {path: '/superAdmin/index', name: '餐饮', component: superAdminIndex,
      children: [
        {
          path: '/superAdmin/mainStore',
          component: superAdminMainStore,
          meta: [
            {name:'店家管理',path:'/superAdmin/mainStore'},
          ],
        },
        {
          path: '/superAdmin/active',
          component: superAdminSystemActive,
          meta: [
            {name:'激活系统',path:'/superAdmin/active'},
          ],
        },

      ]
    },

    //mainStore
    {path: '/mainStore/login', name: '登录', component: mainStoreLogin},
    {path: '/mainStore/index', name: '餐饮', component: mainStoreIndex,
      children: [
        {
          path: '/mainStore/store',
          component: mainStoreStore,
          meta: [
            {name:'分店管理',path:'/mainStore/store'},
          ],
        },
        {
          path: '/mainStore/goodsTag',
          component: mainStoreGoodsTag,
          meta: [
            {name:'菜品标签',path:'/mainStore/goodsTag'},
          ],
        },
        {
          path: '/mainStore/goods',
          component: mainStoreGoods,
          meta: [
            {name:'菜品',path:'/mainStore/goods'},
          ],
        },
        {
          path: '/mainStore/payment',
          component: mainStorePaymentConfig,
          meta: [
            {name:'支付配置',path:'/mainStore/payment'},
          ],
        },
        {
          path: '/mainStore/oauth',
          component: mainStoreOauthConfig,
          meta: [
            {name:'授权配置',path:'/mainStore/oauth'},
          ],
        },
        {
          path: '/mainStore/printerDataModel',
          component: mainStorePrinterDataModel,
          meta: [
            {name:'打印模板配置',path:'/mainStore/printerDataModel'},
          ],
        },
        {
          path: '/mainStore/orders',
          component: mainStoreOrder,
          meta: [
            {name:'订单管理',path:'/mainStore/orders'},
          ],
        },
        {
          path: '/mainStore/orderDetail',
          component: mainStoreOrderDetail,
          meta: [
            {name:'订单详情',path:'/mainStore/orderDetail'},
          ],
        },
        {
          path: '/mainStore/pageView',
          component: mainStorePageView,
          meta: [
            {name:'主题管理',path:'/mainStore/pageView'},
          ],
        },
        {
          path: '/mainStore/pluginsStore',
          component: mainStorePluginsStore,
          meta: [
            {name:'插件市场',path:'/mainStore/pluginsStore'},
          ],
        },{
          path: '/mainStore/chargeItem',
          component: mainStoreChargeItem,
          meta: [
            {name:'充值项配置',path:'/mainStore/chargeItem'},
          ],
        },


      ]
    },


    //store
    {path: '/store/login', name: '登录', component: storeLogin},
    {path: '/store/index', name: '餐饮', component: storeIndex,
      children: [
        {
          path: '/store/goodsTag',
          component: storeGoodsTag,
          meta: [
            {name:'菜品标签',path:'/store/goodsTag'},
          ],
        },
        {
          path: '/store/goods',
          component: storeGoods,
          meta: [
            {name:'菜品',path:'/store/goods'},
          ],
        },
        {
          path: '/store/payment',
          component: storePaymentConfig,
          meta: [
            {name:'支付配置',path:'/store/payment'},
          ],
        },
        {
          path: '/store/oauth',
          component: storeOauthConfig,
          meta: [
            {name:'授权配置',path:'/store/oauth'},
          ],
        },
        {
          path: '/store/printerDataModel',
          component: storePrinterDataModel,
          meta: [
            {name:'打印模板配置',path:'/store/printerDataModel'},
          ],
        },
        {
          path: '/store/printer',
          component: storePrinter,
          meta: [
            {name:'打印机管理',path:'/store/printer'},
          ],
        },
        {
          path: '/store/storeUser',
          component: storeStoreUser,
          meta: [
            {name:'店员管理',path:'/store/storeUser'},
          ],
        },
        {
          path: '/store/tables',
          component: storeTable,
          meta: [
            {name:'餐桌管理',path:'/store/tables'},
          ],
        },
        {
          path: '/store/orders',
          component: storeOrder,
          meta: [
            {name:'订单管理',path:'/store/orders'},
          ],
        },
        {
          path: '/store/setting',
          component: storeSetting,
          meta: [
            {name:'系统设置',path:'/store/setting'},
          ],
        },
        {
          path: '/store/orderDetail',
          component: storeOrderDetail,
          meta: [
            {name:'订单详情',path:'/store/orderDetail'},
          ],
        },
        {
          path: '/store/pageView',
          component: storePageView,
          meta: [
            {name:'主题管理',path:'/store/pageView'},
          ],
        },
        {
          path: '/store/pluginsStore',
          component: storePluginsStore,
          meta: [
            {name:'插件市场',path:'/store/pluginsStore'},
          ],
        },
        {
          path: '/store/callTag',
          component: storeCallTag,
          meta: [
            {name:'呼叫服务',path:'/store/callTag'},
          ],
        },
        {
          path: '/store/callLog',
          component: storeCallLog,
          meta: [
            {name:'呼叫记录',path:'/store/callLog'},
          ],
        },
        {
          path: '/store/chargeItem',
          component: storeChargeItem,
          meta: [
            {name:'充值项配置',path:'/store/chargeItem'},
          ],
        },
        {
          path: '/store/consumer',
          component: storeConsumer,
          meta: [
            {name:'会员管理',path:'/store/consumer'},
          ],
        },
        {
          path: '/store/orderFoodPluginsConfig',
          component: storeOrderFoodPluginsConfig,
          meta: [
            {name:'客人端功能管理',path:'/store/orderFoodPluginsConfig'},
          ],
        },



      ]
    },

    //storeUser
    {path: '/storeUser/login', name: '登录', component: storeUserLogin},
    {path: '/storeUser/index', name: '餐饮', component: storeUserIndex,
      children: [
        {
          path: '/storeUser/cart',
          component: storeUserCart,
          meta: [
            {name:'下单',path:'/storeUser/cart'},
          ],
        },
        {
          path: '/storeUser/orderDetail',
          component: storeUserOrderDetail,
          meta: [
            {name:'订单详情',path:'/storeUser/orderDetail'},
          ],
        },
        {
          path: '/storeUser/orderGoods',
          component: storeUserOrderGoods,
          meta: [
            {name:'菜品队列',path:'/storeUser/orderGoods'},
          ],
        },{
          path: '/storeUser/callLog',
          component: storeUserCallLog,
          meta: [
            {name:'呼叫记录',path:'/storeUser/callLog'},
          ],
        }

      ]
    },

    {path: '/', name: '登录', component: storeUserLogin},
  ]
})



