<template>
    <div class="fillcontain">
        <div class="table_container">
          <el-row>
            <el-col :span="6">
              <el-input placeholder="请输入 名称/昵称/帐号" v-model="pages.keyword" class="input-with-select">
              </el-input>
            </el-col>
            <el-col :span="2">
              <el-button @click="initData">搜索</el-button>
            </el-col>

          </el-row>
          <br/>
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="名称"
                prop="name"
                :show-overflow-tooltip="true"
              >
              </el-table-column>
              <el-table-column
                label="昵称"
                prop="nickName">
              </el-table-column>
              <el-table-column
                label="帐号"
                prop="user.account"
                :show-overflow-tooltip="true"
              >
              </el-table-column>
              <el-table-column
                label="余额(元)">
                <template slot-scope="scope">
                  ￥{{scope.row.user.balance/100}}
                </template>
              </el-table-column>
              <el-table-column
                label="状态"
                :formatter="statusFormat">
              </el-table-column>
              <el-table-column label="操作" width="200">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEditRealPrice(scope.row)">
                    充值
                  </el-button>
                  <el-button
                    v-if="scope.row.black"
                    size="mini"
                    @click="blacklist(scope.row)">
                    移出黑名单
                  </el-button>
                  <el-button
                    v-if="!scope.row.black"
                    size="mini"
                    @click="blacklist(scope.row)">
                    加入黑名单
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                call_tag:{
                    id:0,
                    content:"",
                    status:""+1,
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"新增呼叫标签",
                    close:false,
                },
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                },
              tableData: [],
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/consumers', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.consumers.content;
                    that.pages.total = res.data.consumers.totalElements;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            statusFormat(row,column){
              return (row.user.isFreeze == 0 && row.black == false)?"正常":"封号";
            },
          blacklist(row) {
            var that = this;
            that.$api.get('/store/blacklist', {user_id:row.user.id}, response => {
              if (response.status >= 200 && response.status < 300) {
                console.log(response.data);
                var res = response.data;
                if (res.code == 0) {
                  if(res.data.blacklists.content.length > 0){
                      this.$confirm('要将该用户移出黑名单吗?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                      }).then(() => {

                        this.loading = true;
                        that.$api.delete('/store/blacklist', {user_id: row.user.id}, response => {
                          if (response.status >= 200 && response.status < 300) {
                            console.log(response.data);
                            var res = response.data;
                            if (res.code == 0) {
                              this.$message({
                                message: "移出成功,该用户可正常在本店下单",
                                type: 'success'
                              });
                              this.initData();
                            } else {
                              this.$message({
                                message: res.msg,
                                type: 'error'
                              });
                            }

                            this.loading = false;
                          } else {
                            this.$message({
                              message: "服务器维护中",
                              type: 'error'
                            });

                            this.loading = false;
                          }
                        });

                      }).catch(() => {

                      });
                  } else {
                    this.$confirm('要将该用户加入黑名单吗?', '提示', {
                      confirmButtonText: '确定',
                      cancelButtonText: '取消',
                      type: 'warning'
                    }).then(() => {

                      this.loading = true;
                      that.$api.post('/store/blacklist', {user_id: row.user.id}, response => {
                        if (response.status >= 200 && response.status < 300) {
                          console.log(response.data);
                          var res = response.data;
                          if (res.code == 0) {
                            this.$message({
                              message: "加入成功,该用户将无法在本店下单",
                              type: 'success'
                            });
                            this.initData();
                          } else {
                            this.$message({
                              message: res.msg,
                              type: 'error'
                            });
                          }

                          this.loading = false;
                        } else {
                          this.$message({
                            message: "服务器维护中",
                            type: 'error'
                          });

                          this.loading = false;
                        }
                      });

                    }).catch(() => {

                    });

                    this.loading = false;
                  }
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });

                this.loading = false;
              }
            });
          },
          handleEditRealPrice(row){
            var that = this;
            this.$prompt('请输入充值金额', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
            }).then(({ value }) => {
              that.$api.post('/store/consumers/'+row.id, {chargePrice:value*100}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.initData();
                    this.$message({
                      message: "充值成功",
                      type: 'success'
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                  this.initData();
                }
                this.loading = false;
              });

            }).catch(() => {

            });
          },

        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



