<template>
    <div class="fillcontain">
        <div class="table_container">
          <el-divider content-position="left">订单信息</el-divider>
            <el-row>
              <el-col :span="2">订单号</el-col><el-col :span="4">{{order.orderNo}}</el-col>
              <el-col :span="2">创建时间</el-col><el-col :span="4">{{order.createTimeStr}}</el-col>
              <el-col :span="2">创建者帐号</el-col><el-col :span="4">{{order.createdUser.account}}</el-col>
            </el-row>
            <br/>
            <el-row>
              <el-col :span="2">确认状态</el-col><el-col :span="4">{{order.applyStatusStr}}</el-col>
              <el-col :span="2">就餐人数</el-col><el-col :span="4">{{order.peopleNum}}</el-col>
            </el-row>
            <br/>
            <el-row>
              <el-col>备注: {{order.note}}</el-col>
            </el-row>
          <el-divider content-position="left">支付信息</el-divider>
            <el-row>
              <el-col :span="3">总金额（￥）</el-col><el-col :span="4"> {{order.totalPrice/100}}元</el-col>
              <el-col :span="3">实付金额（￥）</el-col><el-col :span="4">{{order.realPrice/100}}元</el-col>
            </el-row>
            <br/>
            <el-row>
              <el-col :span="2">支付状态</el-col><el-col :span="4"> {{order.payStatusStr}}</el-col>
              <el-col :span="2" v-if="order.payStatus > 1">支付时间</el-col><el-col :span="4" v-if="order.payStatus > 1"> {{order.payTimeStr}}</el-col>
              <el-col :span="2" v-if="order.payStatus > 1">支付方式</el-col><el-col :span="4" v-if="order.payStatus > 1">{{order.payTypeStr}}</el-col>
              <el-col :span="2" v-if="order.payStatus > 1">支付者帐号</el-col><el-col :span="4" v-if="order.payStatus > 1">{{order.payUser.account}}</el-col>
            </el-row>


            <br/>
          <br/>

          <el-table
            :data="goodsData"
            style="width: 100%"
            v-loading="loading">
            <el-table-column
              label="菜名"
              prop="goodsName">
            </el-table-column>

            <el-table-column
              label="编号"
              prop="goods.number">
            </el-table-column>

            <el-table-column
              label="市场价"
              :formatter="markerPriceFormat">
            </el-table-column>

            <el-table-column
              label="销售价"
              :formatter="sellPriceFormat">
            </el-table-column>

            <el-table-column
              label="数量"
              prop="count">
            </el-table-column>

            <el-table-column
              label="备注"
              prop="note"
              :show-overflow-tooltip="true"
            >
            </el-table-column>

            <el-table-column
              label="状态"
              :formatter="statusFormat">
            </el-table-column>
          </el-table>

        </div>



    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                uploadUrl:baseUrl+'/common/imageUpload',
                loading:false,
                order_id:0,
                order:{
                      id:0,
                      number:"",
                      createTimeStr:"",
                      payTime:"",
                      payTimeStr:""
                  },
              goodsData: [],
              totalPrice:0,
              keyword:"",

              chooseOrderGoods:{},
              noteDialogTableVisible:false,
            }
        },
        created(){
            //
          this.order_id = this.$route.query.order_id;
          this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/main_store/orders', {order_id:this.order_id}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.order = res.data.orders.content[0];
                    that.order.createTimeStr = this.dateTimeFormat(that.order.createTime);
                    if(that.order.payTime != null && that.order.payTime != ""){
                      that.order.payTimeStr = this.dateTimeFormat(that.order.payTime);
                    }

                    that.initGoodsData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            initGoodsData(){
              this.loading = true;
              var that = this;
              that.$api.get('/main_store/orders/orderGoods', {order_id:that.order.id}, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.goodsData = res.data.orderGoods;
                    that.totalPrice = 0;
                    that.goodsData.forEach(function(value,i){
                      that.totalPrice += (value.goods.sell_price * value.count);
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            statusFormat(row,column){
                //return row.status == 2?"上架":"下架";
              return row.payStatus == 2?"已退款":"正常";
            },
            markerPriceFormat(row,column){
              return row.goods.marker_price/100;
            },
            sellPriceFormat(row,column){
              return row.goods.sell_price/100;
            },
            totalPriceFormat(row,column){
              return row.goods.sell_price/100;
            },
          dateTimeFormat(time) {
            var date=new Date(time);
            var year=date.getFullYear();
            /* 在日期格式中，月份是从0开始的，因此要加0
             * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
             * */
            var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
            var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
            var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
            var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
            var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
            // 拼接
            return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
            //return year+"-"+month+"-"+day;
          },
            in_array(search,array){
              for(var i in array){
                if(array[i]==search){
                  return true;
                }
              }
              return false;
            },



        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



