<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row>
                <el-button type="primary" @click="handleEdit(0, '','add')">新增分店</el-button>
            </el-row>
            <br/>
            <el-row>
              <el-alert
                :title="'分店管理员登录地址：'+login_url"
                type="success"
                :closable="false">
              </el-alert>
            </el-row>
            <br/>
            <el-table
                :data="userTableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="店名"
                prop="name">
              </el-table-column>

              <el-table-column
                label="账号"
                prop="user.account">
              </el-table-column>

              <el-table-column
                label="电话"
                prop="telephons">
              </el-table-column>


                <el-table-column
                    label="备注"
                    prop="note"
                    :show-overflow-tooltip="true"
                >
                </el-table-column>

                <el-table-column
                    label="状态"
                    :formatter="rreezeFormat">
                </el-table-column>

                <el-table-column label="操作" width="250">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      @click="handleEdit(scope.$index, scope.row,'edit')">
                        编辑
                    </el-button>
                    <el-button
                      size="mini"
                      type="warning"
                      @click="handleResetPwdAlert( scope.row)">
                      重置密码
                    </el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDeleteAlert( scope.row)">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>

            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
                <el-form :model="admin">

                    <el-form-item label="账号" label-width="100px"  v-if="admin.id == 0">
                      <el-input v-model="admin.account" auto-complete="off">
                        <template slot="prepend">{{storePrefix}}</template>
                      </el-input>
                      <br/>
                      <el-alert v-if="admin.account != ''"
                        :title="'帐号为：'+storePrefix+admin.account"
                        type="success"
                                :closable="false"
                      >
                      </el-alert>
                    </el-form-item>
                  <el-form-item label="账号" label-width="100px" v-else>
                    {{admin.account}}
                  </el-form-item>
                    <el-form-item label="密码" label-width="100px" v-if="admin.id == 0">
                      <span>初始密码为：888888</span>
                    </el-form-item>
                    <el-form-item label="店名" label-width="100px">
                        <el-input v-model="admin.name" auto-complete="off"></el-input>
                    </el-form-item>

                  <el-form-item label="省" label-width="100px">
                    <el-input v-model="admin.province" auto-complete="off"></el-input>
                  </el-form-item>

                  <el-form-item label="市" label-width="100px">
                    <el-input v-model="admin.city" auto-complete="off"></el-input>
                  </el-form-item>

                  <el-form-item label="区" label-width="100px">
                    <el-input v-model="admin.area" auto-complete="off"></el-input>
                  </el-form-item>

                  <el-form-item label="地址" label-width="100px">
                    <el-input v-model="admin.address" type="textarea" rows="4"></el-input>
                  </el-form-item>

                  <el-form-item label="电话" label-width="100px">
                    <el-input v-model="admin.telephons" auto-complete="off"></el-input>
                  </el-form-item>


                  <el-form-item label="营业时间" label-width="100px">
                    <el-time-picker
                      v-model="admin.startTime"
                      value-format="HH:mm:ss"
                      placeholder="开始时间">
                    </el-time-picker>
                     至
                    <el-time-picker
                      v-model="admin.endTime"
                      value-format="HH:mm:ss"
                      placeholder="结束时间">
                    </el-time-picker>
                  </el-form-item>

                  <el-form-item label="授权/支付配置" label-width="100px">
                    <el-switch
                      v-model="admin.configType"
                      active-color="#13ce66"
                      inactive-color="#ff4949"
                      active-text="统一配置"
                      inactive-text="独立配置"
                      active-value="1"
                      inactive-value="2">
                    </el-switch>
                  </el-form-item>
                  <el-form-item label="付费方式" label-width="100px">

                    <el-switch
                      v-model="admin.payConfig"
                      active-color="#13ce66"
                      inactive-color="#ff4949"
                      active-text="下单付费"
                      inactive-text="吃完付费"
                      active-value="1"
                      inactive-value="2">
                    </el-switch>
                  </el-form-item>

                  <el-form-item label="后厨出餐模式" label-width="100px">

                    <el-switch
                      v-model="admin.orderGoodsType"
                      active-color="#13ce66"
                      inactive-color="#ff4949"
                      active-text="电脑模式"
                      inactive-text="打印机模式"
                      active-value="1"
                      inactive-value="2">
                    </el-switch>
                  </el-form-item>

                  <el-form-item label="备注" label-width="100px">
                    <el-input v-model="admin.note" type="textarea" rows="4"></el-input>
                  </el-form-item>

                  <el-form-item label="状态" label-width="100px">

                    <el-switch
                      v-model="admin.isFreeze"
                      active-color="#13ce66"
                      inactive-color="#ff4949"
                      active-text="生效"
                      inactive-text="冻结"
                      active-value="0"
                      inactive-value="1">
                    </el-switch>
                  </el-form-item>

                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="handleSave">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                admin:{
                    id:0,
                    account:"",
                    name:"",
                    province:"",
                    city:"",
                    area:"",
                    address:"",
                    telephons:"",
                    note:"",
                    isFreeze:"0",
                    startTime:"",
                    endTime:"",
                    configType:"1",
                    payConfig:"1",
                    orderGoodsType:"1",
                },
                storePrefix:"",
                dialog:{
                    dialogFormVisible:false,
                    title:"新增分店",
                    close:false,
                },
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                },
                userTableData: [],
                login_url:"",

            }
        },
        created(){
            var storage = window.localStorage;
            var user = JSON.parse(storage["user"]);
            this.storePrefix = user.storePrefix;
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/main_store/store', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.userTableData = res.data.stores.content;
                    that.login_url = res.data.login_url;
                    that.pages.total = res.data.stores.totalElements;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            rreezeFormat(row,column){
                return row.user.isFreeze == 1?"冻结":"正常";
            },
            handleEdit(index, row,action) {
                if(action == "edit"){
                    this.dialog.title = "修改分店";
                    this.admin.id = row.id;
                    this.admin.account = row.user.account;
                    this.admin.name = row.name;
                    this.admin.province = row.province;
                    this.admin.city = row.city;
                    this.admin.area = row.area;
                    this.admin.address = row.address;
                    this.admin.telephons = row.telephons;
                    this.admin.note = row.note;
                    this.admin.startTime = row.startTime;
                    this.admin.endTime = row.endTime;
                    this.admin.configType = ""+row.configType;
                    this.admin.payConfig = ""+row.payConfig;
                    this.admin.isFreeze = ""+row.user.isFreeze;
                    this.admin.orderGoodsType = ""+row.orderGoodsType;
                    this.dialog.dialogFormVisible = true;

                }else if(action == "add"){
                    this.dialog.title = "新增分店";
                    this.admin.id = 0;
                    this.admin.account = "";
                    this.admin.name = "";
                    this.admin.province = "";
                    this.admin.city = "";
                    this.admin.area = "";
                    this.admin.address = "";
                    this.admin.telephons = "";
                    this.admin.note = "";
                    this.admin.startTime = "";
                    this.admin.endTime = "";
                    this.admin.configType = "1";
                    this.admin.payConfig = "1";
                    this.admin.isFreeze = "0";
                    this.admin.orderGoodsType = "1";
                    this.dialog.dialogFormVisible = true;
                }
            },
            handleSave() {
                this.loading = true;
                var that = this;
                that.$api.post('/main_store/store', that.admin, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },

          handleResetPwdAlert(row) {
            this.$confirm('此操作将重置该分店管理员的密码为:888888, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.handleResetPwd(row);
            }).catch(() => {

            });
          },

          handleResetPwd(row) {
            this.loading = true;
            var that = this;
            that.$api.put('/main_store/store/reset_password/'+row.id, null, response => {
              if (response.status >= 200 && response.status < 300) {
                console.log(response.data);
                var res = response.data;
                if (res.code == 0) {
                  this.$message({
                    message: '重置成功,请立即使用默认密码:888888 登录修改密码',
                    type: 'success',

                  });
                  //this.initData();
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });
              }

              this.loading = false;

            });
          },
            handleDeleteAlert(row) {
                this.$confirm('此操作将删除该分店, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.handleDelete(row);
                }).catch(() => {

                });
            },
          handleDelete(row) {
            this.loading = true;
            var that = this;
            that.$api.delete('/main_store/store/'+row.id, null, response => {
              if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '删除成功',
                      type: 'success'
                    });
                    this.loading = false;
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
              } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
              }

            });
            },
        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



