<template>
    <div class="fillcontain">
        <div class="table_container">

            <!--<el-row>-->
              <!--<el-col :span="8">-->
                <!--<el-input placeholder="请输入插件名称" v-model="pages.keyword" class="input-with-select">-->
                <!--</el-input>-->
              <!--</el-col>-->
              <!--<el-col :span="3">-->
                <!--<el-button @click="initData">搜索</el-button>-->
              <!--</el-col>-->
            <!--</el-row>-->
            <!--<br/>-->

          <br/>

        <el-table
          :data="pluginsTypes"
          style="width: 100%"
          v-loading="loading">
          <el-table-column
            type="expand">
              <template slot-scope="props">
                <el-table
                  :data="plugins[props.row.type]"
                  style="width: 100%"
                  v-loading="loading">
                  <el-table-column
                    label="插件名"
                    prop="name">
                  </el-table-column>
                  <el-table-column
                    label="价格"
                    :formatter="vip0PriceFormat">
                  </el-table-column>
                  <el-table-column
                    label="年费会员价"
                    :formatter="vip1PriceFormat">
                  </el-table-column>
                  <el-table-column
                    label="有效期"
                    :formatter="dayFormat">
                  </el-table-column>
                  <el-table-column
                    label="状态">
                    <template slot-scope="scope">
                      <span v-if="scope.row.status == 1" style="color: red">未购买</span>
                      <span v-else-if="scope.row.status == 2" style="color: green">
                        已购买(生效时间:{{scope.row.startTime}})
                      </span>
                      <span v-else-if="scope.row.status == 3" style="color: red">已过期:{{scope.row.endTime}}</span>
                      <span v-else-if="scope.row.status == 4" style="color: green"><span v-if="scope.row.test" style="color: red">试用中</span><span v-else style="color: green">已生效</span><span v-if="scope.row.day > 0">({{scope.row.endTime}})</span></span>
                    </template>
                  </el-table-column>
                  <el-table-column
                    label="备注"
                    :show-overflow-tooltip="true"
                    prop="note">
                  </el-table-column>
                  <el-table-column label="操作" width="200">
                    <template slot-scope="scope">
                      <div>
                        <el-button
                          size="mini"
                          type="danger"
                          v-if="scope.row.status == 1 || scope.row.status == 3"
                          @click="buy(scope.$index, scope.row,'edit')">
                          购买
                        </el-button>
                        <el-button
                          size="mini"
                          type="danger"
                          v-else-if="(scope.row.status == 2 || scope.row.status == 4) && scope.row.day > 0"
                          @click="buy(scope.$index, scope.row,'edit')">
                          延期
                        </el-button>
                      </div>
                    </template>
                  </el-table-column>
                </el-table>
              </template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="插件">
          </el-table-column>
        </el-table>




            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
                <el-form :model="plugin">

                    <el-form-item label="插件名" label-width="80px">
                        {{plugin.name}}
                    </el-form-item>
                  <el-form-item label="价格" label-width="80px">
                    {{plugin.price}}元
                  </el-form-item>

                  <el-form-item label="有效期" label-width="80px">
                    <span v-if="plugin.day == 0">永久</span>
                    <span v-else>{{plugin.day*plugin.count}}天</span>
                  </el-form-item>
                  <el-form-item label="数量" label-width="80px" v-if="plugin.day > 0">
                    <el-input-number v-model="plugin.count" :min="1" :max="10000"></el-input-number>
                    <el-alert
                      title="多份数量时，有效期会自动叠加"
                      type="success">
                    </el-alert>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <a :href="baseUrl+'/payment/plugins/'+plugin.uuid+'?token='+token+'&count='+plugin.count" target="_blank">
                      <el-button type="primary">确定购买</el-button>
                    </a>
                </div>
            </el-dialog>


          <el-dialog title="绑定微信" v-model="wxDialogVisible" :close-on-click-modal="dialog.close">
            <div style="width: 100%;text-align: center">
              <img v-bind:src="qrcodeurl">
            </div>
            <div style="width: 100%;text-align: center">
              <span>用该员工的微信扫一扫二维码进行微信登录授权绑定</span>
            </div>

            <div slot="footer" class="dialog-footer">
              <el-button @click="wxDialogVisible = false">关闭</el-button>
            </div>
          </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                uploadUrl:baseUrl+'/common/imageUpload',
                loading:false,
                qrcodeurl:"",
                plugin:{
                      uuid:0,
                      name:"",
                      day:0,
                      vip0Price:0,
                      vip1Price:0,
                      count:1,
                  },
                  dialog:{
                      dialogFormVisible:false,
                      title:"购买插件",
                      close:false,
                  },
                  // pages:{
                  //     offset: 0,
                  //     pageSize: 10,
                  //     page: 1,
                  //     total: 0,
                  //     keyword:"",
                  // },
                pluginsTypes: [],
                plugins:[],

            }
        },
        created(){
            var storage = window.localStorage;
            this.token = storage["access_token"];
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/main_store/plugins', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {

                    that.pluginsTypes = res.data.pluginsTypes;
                    that.plugins = res.data.plugins;

                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            // handleSizeChange(val) {
            //     console.log(`每页 ${val} 条`);
            // },
            // handleCurrentChange(val) {
            //     this.pages.page = val;
            //     this.pages.offset = (val - 1)*this.pages.pageSize;
            //     this.initData()
            // },
            // statusFormat(row,column){
            //     return row.status == 2?"上架":"下架";
            // },
            dayFormat(row,column){
              return row.day == 0?"永久":row.day+"天";
            },

            vip0PriceFormat(row,column){
              return row.vip0Price/100;
            },

            vip1PriceFormat(row,column){
              return row.vip1Price/100;
            },
            vip2PriceFormat(row,column){
              return row.vip2Price/100;
            },

          buy(index, row,action) {
              this.plugin.uuid = row.uuid;
              this.plugin.name = row.name;
              this.plugin.day = row.day;
              this.plugin.price = row.price/100;
              this.plugin.vip0Price = row.vip0Price/100;
              this.plugin.vip1Price = row.vip1Price/100;
              this.plugin.vip2Price = row.vip2Price/100;
              this.plugin.count = 1;
              this.dialog.dialogFormVisible = true;
            },


        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



