<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row>
                <el-button type="primary" @click="handleEdit(0, '','add')">新增餐桌</el-button>
                <el-button type="warning" @click="showQr=!showQr"><span v-if="showQr == false">二维码展示</span><span v-else>列表展示</span></el-button>
            </el-row>
            <br/>
          <el-row v-if="showQr == true">
            <el-alert
              title="可通过浏览器打印当前页面的二维码"
              type="success">
            </el-alert>
          </el-row>
          <el-row v-if="showQr == true">
            <el-col :span="6" v-for="(item, key) in tableData" :key="key">
              <el-card style="margin: 5px;height: 320px">
                <div style="text-align: center">
                  <span>{{item.name}}</span>
                </div>
                <img :src="item.qrPic" class="image">
                <div style="text-align: center">
                  <span>{{item.number}}</span>
                </div>
              </el-card>
            </el-col>
          </el-row>

          <el-table
                v-if="showQr == false"
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="餐桌名"
                prop="name">
              </el-table-column>

              <el-table-column
                label="餐桌编号"
                prop="number">
              </el-table-column>

              <el-table-column
                label="uuid"
                prop="uuid">
              </el-table-column>

              <el-table-column
                label="备注"
                prop="note"
                :show-overflow-tooltip="true"
              >
              </el-table-column>
              <el-table-column
                label="负责人"
                prop="storeUser.name">
              </el-table-column>

                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      @click="handleEdit(scope.$index, scope.row,'edit')">
                        编辑
                    </el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDeleteAlert( scope.row)">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>

            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
                <el-form :model="table">
                    <el-form-item label="餐桌名" label-width="120px">
                        <el-input v-model="table.name" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="餐桌编号" label-width="120px">
                      <el-input v-model="table.number" auto-complete="off"></el-input>
                    </el-form-item>
                  <el-form-item label="最少可坐人数" label-width="120px">
                    <el-input-number v-model="table.minPeopleCount" :min="1" :max="10000" @change="handleMinPeopleCountChange"></el-input-number>
                  </el-form-item>
                  <el-form-item label="最多可坐人数" label-width="120px">
                    <el-input-number v-model="table.maxPeopleCount" :min="table.minPeopleCount" :max="10000"></el-input-number>
                  </el-form-item>
                  <el-form-item label="备注" label-width="100px">
                    <el-input v-model="table.note" type="textarea" rows="4"></el-input>
                  </el-form-item>
                  <el-form-item label="负责人" label-width="120px">
                    <el-input v-model="table.storeUserNumber" auto-complete="off" placeholder="输入负责该桌的店员编号"></el-input>
                  </el-form-item>

                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="handleSave">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
              showQr:false,
                table:{
                    id:0,
                    name:"",
                    number:"",
                    minPeopleCount:1,
                    maxPeopleCount:1,
                    note:"",
                    storeUserNumber:"",
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"新增餐桌",
                    close:false,
                },
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                },
              tableData: [],
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/tables', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.tables.content;
                    this.tableData.forEach(function(value,i){
                        value['qrPic'] = that.baseUrl+"/common/qrCode?content="+value['qrPic'];
                    });
                    that.pages.total = res.data.tables.totalElements;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            handleMinPeopleCountChange(value){
              if(value > this.table.maxPeopleCount){
                this.table.maxPeopleCount = value;
              }
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            handleEdit(index, row,action) {
                if(action == "edit"){
                    this.dialog.title = "修改餐桌";
                    this.table.id = row.id;
                    this.table.name = row.name;
                    this.table.number = row.number;
                    this.table.note = row.note;
                    this.table.minPeopleCount=row.minPeopleCount;
                    this.table.maxPeopleCount=row.maxPeopleCount;
                    this.table.storeUserNumber = row.storeUser == null ? "":row.storeUser.number;
                    this.dialog.dialogFormVisible = true;
                }else if(action == "add"){
                    this.dialog.title = "修改餐桌";
                    this.table.id = 0;
                    this.table.name = "";
                    this.table.number = "";
                    this.table.note = "";
                    this.table.storeUserNumber = "";
                    this.dialog.dialogFormVisible = true;
                    this.table.minPeopleCount=1;
                    this.table.maxPeopleCount=1;
                }
            },
            downloadQrCode(){
                location.href="";
            },

            handleSave() {
                this.loading = true;
                var that = this;
                that.$api.post('/store/tables', that.table, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },
            handleDeleteAlert(row) {
                this.$confirm('此操作将删除该餐桌, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.handleDelete(row);
                }).catch(() => {

                });
            },
          handleDelete(row) {
            this.loading = true;
            var that = this;
            that.$api.delete('/store/tables/'+row.id, null, response => {
              if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '删除成功',
                      type: 'success'
                    });
                    this.loading = false;
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
              } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
              }

            });
            },
        },
    }
</script>
<style lang="less">
  @import '../../style/mixin';
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .table_container{
    padding: 20px;
    text-align: left;
  }
  .Pagination{
    display: flex;
    justify-content: flex-start;
    margin-top: 8px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
  }
  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }
</style>

<style>
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 10px;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>



