<template>
  <div class="fillcontain">
    <div class="table_container">
      <el-table
        :data="tableData"
        style="width: 100%"
        v-loading="loading">
        <el-table-column
          label="名称"
          prop="name">
        </el-table-column>

        <el-table-column
          label="使用状态"
          :formatter="statusFormat">
        </el-table-column>
        <el-table-column
          label="插件状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status == 4" style="color: green">{{scope.row.statusStr}}</span>
            <span v-else-if="scope.row.status == 1" style="color: red">{{scope.row.statusStr}}</span>
            <span v-else-if="scope.row.status == 2" style="color: saddlebrown">{{scope.row.statusStr}}</span>
            <span v-else-if="scope.row.status == 3" style="color: red">{{scope.row.statusStr}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button
              v-if="scope.row.status == 4"
              size="mini"
              @click="handleEdit(scope.$index, scope.row,'edit')">
              配置
            </el-button>
            <el-button
              v-if="scope.row.status == 1 || scope.row.status == 3"
              size="mini"
              type="danger"
              @click="goBuy(scope.row)">
              购买
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
        <el-form :model="plugin">
          <el-form-item :label="key" label-width="100px" v-for="(item,key) in params" :key="key">
            <el-input v-model="item.value" auto-complete="off" :placeholder="item.placeholder"></el-input>
          </el-form-item>
          <el-form-item label="状态" label-width="80px">

            <el-switch
              v-model="plugin.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="使用"
              inactive-text="不使用"
              active-value="true"
              inactive-value="false">
            </el-switch>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="handleSave">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
  export default {
    data(){
      return {
        baseUrl,
        baseImgPath,
        loading:false,
        plugin:{
          id:0,
          params:"",
          status:false,
        },
        dialog:{
          dialogFormVisible:false,
          title:"配置",
          close:false,
        },
        tableData: [],
        params:[],
      }
    },
    created(){
      this.initData();
    },
    methods: {
      initData(){
        this.loading = true;
        var that = this;
        that.$api.get('/store/order_food_plugin_config', that.pages, response => {
          if (response.status >= 200 && response.status < 300) {
            var res = response.data;
            if (res.code == 0) {
              that.tableData = res.data.plugins;
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }
          this.loading = false;
        });
      },

      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.page = val;
        this.pages.offset = (val - 1)*this.pages.pageSize;
        this.initData()
      },
      statusFormat(row,column){
        return row.used == true?"使用中":"未使用";
      },
      goBuy(row){
        this.$router.push({path:'/store/pluginsStore',query:{uuid:row.uuid}});
      },
      handleEdit(index, row,action) {
        this.dialog.title = "配置:"+row.name;
        this.plugin.id = row.id;
        this.plugin.name = row.name;
        this.plugin.status = ""+row.used;
        this.params = row.paramsMap;
        // for(var key in row.paramsMap){
        //   this.params.push({name:key,value:row.paramsMap[key]['value'],placeholder:row.paramsMap[key]['placeholder']});
        // }

        this.dialog.dialogFormVisible = true;
      },
      handleSave() {
        this.loading = true;
        var that = this;


        that.plugin.params = JSON.stringify(this.params);


        that.$api.put('/store/order_food_plugin_config/'+that.plugin.id, that.plugin, response => {
          if (response.status >= 200 && response.status < 300) {
            console.log(response.data);
            var res = response.data;
            if (res.code == 0) {
              this.$message({
                message: that.dialog.title+'成功',
                type: 'success'
              });
              this.dialog.dialogFormVisible = false;
              this.initData();
            }else{
              this.$message({
                message: res.msg,
                type: 'warning'
              });
            }
          } else {
            this.$message({
              message: "服务器维护中",
              type: 'error'
            });
          }

        });

        this.loading = false;
      },
    },
  }
</script>

<style lang="less">
  @import '../../style/mixin';
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .table_container{
    padding: 20px;
    text-align: left;
  }
  .Pagination{
    display: flex;
    justify-content: flex-start;
    margin-top: 8px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
  }
  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }
</style>



