<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row>
              <el-col :span="4">
                <el-select v-model="pages.payStatus" placeholder="全部状态" style="width:100%;" >
                  <el-option
                    v-for="item in status"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="6">
                <el-input placeholder="请输入订单号/餐桌号查询" v-model="pages.keyword" class="input-with-select">
                </el-input>
              </el-col>

              <el-col :span="10">
                <el-date-picker
                  v-model="pages.created_start_time"
                  type="datetime"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  placeholder="开始时间">
                </el-date-picker>

                <el-date-picker
                  v-model="pages.created_end_time"
                  type="datetime"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  placeholder="结束时间">
                </el-date-picker>
              </el-col>
              <el-col :span="2">
                <el-button @click="initData">搜索</el-button>
              </el-col>

            </el-row>
            <br/>
            <el-row>
              <el-col :span="3">
                <el-button type="primary" @click="exportExcel">导出订单</el-button>
              </el-col>
            </el-row>
            <br/>
            <el-table
                :data="orderData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="订单号"
                prop="orderNo">

              </el-table-column>

              <el-table-column
                label="类型">
                <template slot-scope="scope">
                  <span v-if="scope.row.type == 1">
                    点餐,餐桌号:{{scope.row.tableNumber}}
                  </span>
                  <span v-else="scope.row.type == 3">
                    余额充值
                  </span>
                </template>


              </el-table-column>
              <el-table-column
                label="三方订单号"
                prop="outOrderNo">
              </el-table-column>
              <el-table-column
                label="支付状态"
                prop="payStatusStr">
              </el-table-column>
              <el-table-column
                label="确单状态"
                :formatter="applyStatusFormat">
              </el-table-column>
              <el-table-column
                label="金额(￥)"
                :formatter="realPriceFormat">
              </el-table-column>


              <el-table-column
                label="创建者"
                :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="danger"
                    @click="blacklist( scope.row)">
                    {{ scope.row.createdUser.account }}
                  </el-button>
                </template>
              </el-table-column>



                <el-table-column label="操作" width="220">
                  <template slot-scope="scope">

                    <el-button
                      size="mini"
                      @click="handleInfo(scope.row)">
                      详情
                    </el-button>
                    <el-button
                      size="mini"
                      v-if="scope.row.payStatus == 1"
                      @click="sureOrderHandlerAlert(scope.row)">
                      确单
                    </el-button>
                    <el-button
                      size="mini"
                      v-if="scope.row.payStatus == 1 && scope.row.applyStatus != 3"
                      @click="handleEditRealPrice(scope.row)">
                      改价
                    </el-button>
                    <el-button
                      size="mini"
                      @click="openChoosePrinter(scope.row)"
                      v-if="scope.row.applyStatus != 3"
                    >
                        打印
                    </el-button>




                    <el-button
                      size="mini"
                      type="danger"
                      v-if="scope.row.payStatus != 2"
                      @click="handleDeleteAlert( scope.row)">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>
          <el-dialog title="打印" :visible.sync="printerDialogTableVisible">
            <el-divider content-position="left">打印机</el-divider>
            <div v-if="printers.length > 0">
              <el-radio v-model="choosePrinter" :label="item" border v-for="(item,key) in printers" :key="key" @change="handlePrinterChange">{{item.name}}</el-radio>
            </div>
            <div v-else>
              暂无打印机
            </div>
            <el-divider content-position="left">打印模板</el-divider>
            <div v-if="choosePrinter != null && choosePrinter.printerDataModels != null">
              <el-radio v-model="choosePrinterDataModels" :label="item" border v-for="(item,key) in choosePrinter.printerDataModels" :key="key">{{item.name}}</el-radio>
            </div>
            <div v-else>
              暂无打印模板
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="printerDialogTableVisible = false">取 消</el-button>
                <el-button type="primary" @click="printOrder" v-if="choosePrinter != null && choosePrinterDataModels != null">确 定</el-button>
              </span>
          </el-dialog>


          <el-dialog title="确认订单" :visible.sync="sureOrderDialogVisible"
            width="30%">
            <span>为防止恶意刷单，请确认当前订单是否有效,如发现有人恶意刷单，可点击列表中的【创建者】的帐号将其加入黑名单</span>
            <br/>
            <br/>
            <el-select v-model="sureOrder.status" placeholder="请选择">
              <el-option
                v-for="item in sureOrderStatus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <span slot="footer" class="dialog-footer">
              <el-button @click="sureOrderDialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="sureOrderHandler">确 定</el-button>
            </span>
          </el-dialog>




        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                order:{},
                dialog:{
                    dialogFormVisible:false,
                    title:"新增餐桌",
                    close:false,
                },
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                    payStatus:0,
                    keyword:"",
                    created_start_time:"",
                    created_end_time:"",
                },
              orderData: [],
              status:[
                {id:0,name:"全部状态"},
                {id:1,name:"待支付"},
                {id:2,name:"已支付"},
                {id:3,name:"已退款"},
                ],
              choosePrinter:{},
              choosePrinterDataModels:null,
              printers:[],
              printerDialogTableVisible:false,
              sureOrder:{
                  order_id:0,
                  status:"2",
              },
              sureOrderDialogVisible:false,
              sureOrderStatus: [{
                value: '2',
                label: '确定为有效订单'
              }, {
                value: '3',
                label: '拒绝接受该订单'
              }],

            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/orders', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.orderData = res.data.orders.content;
                    that.pages.total = res.data.orders.totalElements;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            handleInfo(row){
              this.$router.push({path:'/store/orderDetail',query: {order_id: row.id}});
            },

            handleEditRealPrice(row){
              var that = this;
              this.$prompt('请输入实收金额', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
              }).then(({ value }) => {
                that.$api.put('/store/orders/'+row.id, {real_price:value*100}, response => {
                  if (response.status >= 200 && response.status < 300) {
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: "改价成功",
                        type: 'success'
                      });
                      that.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                    this.initData();
                  }
                  this.loading = false;
                });

              }).catch(() => {

              });
            },

            openChoosePrinter(row){
              this.order = row;
              var that = this;
              that.$api.get('/store/printer', null, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.printers = res.data.printers;
                    this.printerDialogTableVisible = true;
                    this.choosePrinter = null;
                    this.choosePrinterDataModels = null;

                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            handlePrinterChange(){
              this.choosePrinterDataModels = null;
            },

            printOrder(){
                var that = this;
                that.$api.put('/store/printer/'+that.choosePrinter.id+'/order/'+this.order.id+'/type/'+this.choosePrinterDataModels.type,null, response => {
                  if (response.status >= 200 && response.status < 300) {
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: "已加入到打印队列了",
                        type: 'success'
                      });
                      that.printerDialogTableVisible = false;
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }
                  this.loading = false;
                });
            },


            exportExcel(){
              if(this.pages.created_start_time == "" || this.pages.created_end_time == ""){
                this.$message({
                  message: "导出订单请先选择时间区间",
                  type: 'warning'
                });
                return;
              }
              var storage=window.localStorage;
              var accessToken = storage["access_token"];
              location.href=baseUrl+"/store/orders/excel?access_token="+accessToken+"&keyword="+this.pages.keyword+"&status="+this.pages.payStatus+"&created_start_time="+this.pages.created_start_time+"&created_end_time="+this.pages.created_end_time;
            },

            realPriceFormat(row,column){
              if((row.realPrice - row.refundPrice)== row.totalPrice){
                return row.realPrice/100+"元";
              }else if(row.refundPrice == 0){
                return "实收:"+row.realPrice/100+"元/总价:"+row.totalPrice/100+"元";
              }else{
                return "实收:"+row.realPrice/100+"元/退款"+row.refundPrice/100+"元/总价:"+row.totalPrice/100+"元";
              }

            },
            applyStatusFormat(row,column){
              if(row.applyStatus == 1){
                return "待确认";
              }else if(row.applyStatus == 2){
                return "已确认";
              }else{
                return "已拒绝";
              }
            },
            handleDeleteAlert(row) {
                this.$confirm('此操作将删除该订单吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.handleDelete(row);
                }).catch(() => {

                });
            },
          blacklist(row) {
            this.loading = true;
            var that = this;
            that.$api.get('/store/blacklist', {user_id:row.createdUser.id}, response => {
              if (response.status >= 200 && response.status < 300) {
                console.log(response.data);
                var res = response.data;
                if (res.code == 0) {
                  this.$confirm('要将该用户移出黑名单吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                  }).then(() => {

                    that.$api.delete('/store/blacklist', {user_id:row.createdUser.id}, response => {
                      if (response.status >= 200 && response.status < 300) {
                        console.log(response.data);
                        var res = response.data;
                        if (res.code == 0) {
                          this.$message({
                            message: "移出成功,该用户可正常在本店下单",
                            type: 'success'
                          });

                        }else{
                          this.$message({
                            message: res.msg,
                            type: 'error'
                          });
                        }

                        this.loading = false;
                      } else {
                        this.$message({
                          message: "服务器维护中",
                          type: 'error'
                        });

                        this.loading = false;
                      }
                    });

                  }).catch(() => {

                  });
                }else{
                  this.$confirm('要将该用户加入黑名单吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                  }).then(() => {
                    that.$api.post('/store/blacklist', {user_id:row.createdUser.id}, response => {
                      if (response.status >= 200 && response.status < 300) {
                        console.log(response.data);
                        var res = response.data;
                        if (res.code == 0) {
                          this.$message({
                            message: "加入成功,该用户将无法在本店下单",
                            type: 'success'
                          });
                        }else{
                          this.$message({
                            message: res.msg,
                            type: 'error'
                          });
                        }

                        this.loading = false;
                      } else {
                        this.$message({
                          message: "服务器维护中",
                          type: 'error'
                        });

                        this.loading = false;
                      }
                    });

                  }).catch(() => {

                  });

                  this.loading = false;
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });

                this.loading = false;
              }
            });
          },
          sureOrderHandlerAlert(row){
            this.sureOrder.id = row.id;
            var status = row.applyStatus == 1?"2":""+row.applyStatus;
            this.sureOrder.status=""+row.applyStatus;
            this.sureOrderDialogVisible = true;
          },
          sureOrderHandler(){//确单操作
            var that = this;
            that.$api.put('/store/orders/'+this.sureOrder.id+"/status",{applyStatus:this.sureOrder.status}, response => {
              if (response.status >= 200 && response.status < 300) {
                var res = response.data;
                if (res.code == 0) {
                  this.$message({
                    message: "已更新当前订单状态",
                    type: 'success'
                  });
                  this.sureOrderDialogVisible = false;
                  this.initData();
                }else{
                  this.$message({
                    message: res.msg,
                    type: 'warning'
                  });
                }
              } else {
                this.$message({
                  message: "服务器维护中",
                  type: 'error'
                });
              }
              this.loading = false;
            });
          },
          handleDelete(row) {
            this.loading = true;
            var that = this;
            that.$api.delete('/store/orders/'+row.id, null, response => {
              if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '删除成功',
                      type: 'success'
                    });
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });

                    this.loading = false;
                  }
              } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });

                this.loading = false;
              }

            });
            },


        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



