<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row v-if="plugin_msg != null">
              <el-alert
                :title="plugin_msg"
                type="error">
              </el-alert>
            </el-row>
          <br/>
          <el-row>
          <el-alert
            title="客人可在移动端直接进行充值获得赠送金额"
            type="success">
          </el-alert>
          </el-row>
          <br/>
            <el-row v-if="showAddBtn">
                <el-button type="primary" @click="handleEdit(0, '','add')">新增充值项</el-button>
            </el-row>

          <br/>
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="充值金额"
                prop="chargePrice">
              </el-table-column>
              <el-table-column
                label="得到金额"
                prop="totalPrice">
              </el-table-column>
              <el-table-column
                label="标签"
                prop="tag">
              </el-table-column>
              <el-table-column
                label="状态"
                :formatter="statusFormat">
              </el-table-column>
                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      @click="handleEdit(scope.$index, scope.row,'edit')">
                        编辑
                    </el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDeleteAlert( scope.row)">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="pages.page"
                  :page-size="pages.pageSize"
                  layout="total, prev, pager, next"
                  :total="pages.total">
                </el-pagination>
            </div>

            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
                <el-form :model="charge_item">
                    <el-form-item label="充值金额" label-width="100px">
                        <el-input-number v-model="charge_item.chargePrice" auto-complete="off" :min="1"></el-input-number>
                    </el-form-item>
                    <el-form-item label="得到金额" label-width="100px">
                      <el-input-number v-model="charge_item.totalPrice" auto-complete="off" :min="1"></el-input-number>
                    </el-form-item>
                    <el-form-item label="标签" label-width="100px">
                      <el-input v-model="charge_item.tag" auto-complete="off" placeholder="如:开业"></el-input>
                    </el-form-item>

                    <el-form-item label="状态" label-width="80px">

                      <el-switch
                        v-model="charge_item.status"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        active-text="上线"
                        inactive-text="下线"
                        active-value="2"
                        inactive-value="1">
                      </el-switch>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="handleSave">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                showAddBtn:true,
                charge_item:{
                    id:0,
                    chargePrice:"",
                    totalPrice:"",
                    tag:"",
                    status:""+1,
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"新增充值项",
                    close:false,
                },
                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                },
              tableData: [],
              plugin_msg:null,

            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/main_store/charge_item', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.charge_items.content;
                    that.plugin_msg = res.data.plugin_msg;
                    that.pages.total = res.data.charge_items.totalElements;
                  }else{
                    this.showAddBtn = false;
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.showAddBtn = false;
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            statusFormat(row,column){
              return row.status == 2?"上架":"下架";
            },
            handleEdit(index, row,action) {
                if(action == "edit"){
                    this.dialog.title = "修改充值项";
                    this.charge_item.id = row.id;
                    this.charge_item.chargePrice = row.chargePrice;
                    this.charge_item.totalPrice = row.totalPrice;
                    this.charge_item.tag = row.tag;
                    this.charge_item.status = ""+row.status;
                    this.dialog.dialogFormVisible = true;
                }else if(action == "add"){
                    this.dialog.title = "增加充值项";
                    this.charge_item.id = 0;
                    this.charge_item.chargePrice = "";
                    this.charge_item.totalPrice = "";
                    this.charge_item.tag = "";
                    this.charge_item.status = "1";
                    this.dialog.dialogFormVisible = true;
                }
            },
            handleSave() {
                this.loading = true;
                var that = this;
                that.$api.post('/main_store/charge_item', that.charge_item, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },
            handleDeleteAlert(row) {
                this.$confirm('此操作将删除该菜品标签, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.handleDelete(row);
                }).catch(() => {

                });
            },
          handleDelete(row) {
            this.loading = true;
            var that = this;
            that.$api.delete('/main_store/charge_item/'+row.id, null, response => {
              if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '删除成功',
                      type: 'success'
                    });
                    this.loading = false;
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
              } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
              }

            });
            },
        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



