<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-alert
              title="当分店统一使用支付配置时，此配置会在分店的订单支付时生效"
              type="success">
            </el-alert>
          <br/>
            <el-alert
              :title="'支付jsapi地址:'+jsapi_url"
              type="success">
            </el-alert>
            <br/>
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="支付方式"
                prop="name">
              </el-table-column>

              <el-table-column
                label="使用状态"
                :formatter="statusFormat">
              </el-table-column>
              <el-table-column
                label="插件状态">
                <template slot-scope="scope">
                  <span v-if="scope.row.plugins.status == 4" style="color: green">{{scope.row.plugins.statusStr}}</span>
                  <span v-else-if="scope.row.plugins.status == 1" style="color: red">{{scope.row.plugins.statusStr}}</span>
                  <span v-else-if="scope.row.plugins.status == 2" style="color: saddlebrown">{{scope.row.plugins.statusStr}}</span>
                  <span v-else-if="scope.row.plugins.status == 3" style="color: red">{{scope.row.plugins.statusStr}}</span>
                </template>
              </el-table-column>
                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button
                      v-if="scope.row.plugins.status == 4"
                      size="mini"
                      @click="handleEdit(scope.$index, scope.row,'edit')">
                        配置
                    </el-button>
                    <el-button
                      v-if="scope.row.plugins.status == 1 || scope.row.plugins.status == 3"
                      size="mini"
                      type="danger"
                      @click="goBuy(scope.row.plugins)">
                      购买
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>

            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close">
                <el-form :model="payment">
                    <el-form-item :label="key" label-width="100px" v-for="(item,key) in params"  :key="key">
                        <el-input v-model="item.value" auto-complete="off" :placeholder="item.placeholder" v-if="item.ele_type == 'text'"></el-input>
                        <div v-if="item.ele_type == 'file'">
                          <el-upload
                            v-if="item.value == ''"
                            :action="uploadUrl+'?key='+key"
                            :on-success="handleSuccess"
                            :limit="1">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">{{item.placeholder}}</div>
                          </el-upload>
                          <div v-else>
                            已上传{{key}}文件  <el-button size="small" type="danger" @click="item.value = ''">删除</el-button>
                          </div>
                        </div>



                    </el-form-item>
                    <el-form-item label="状态" label-width="80px">

                      <el-switch
                        v-model="payment.status"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        active-text="使用"
                        inactive-text="不使用"
                        active-value="2"
                        inactive-value="1">
                      </el-switch>
                    </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="handleSave">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                uploadUrl:baseUrl+'/common/imageUpload',
                keyUploadUrl:"",
                loading:false,
                payment:{
                    id:0,
                    params:"",
                    status:1,
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"新增支付",
                    close:false,
                },
              tableData: [],
              params:[],
              jsapi_url:"",
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/main_store/payment_config', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.paymentsConfigs;
                    that.jsapi_url = res.data.jsapi_url;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },

            handleRemove(file, fileList) {
              console.log(file, fileList);
            },
            handlePreview(file) {
              console.log(file);
            },
            handleExceed(files, fileList) {
              //this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            beforeRemove(file, fileList) {
              return this.$confirm(`确定移除 ${ file.name }？`);
            },
            statusFormat(row,column){
              return row.status == 2?"使用中":"未使用";
            },
            goBuy(row){
              this.$router.push({path:'/mainStore/pluginsStore',query:{uuid:row.uuid}});
            },
            handleSuccess(response,file, fileList) {
              //file.id = response.ids[0];
              //file.ext = response.exts[0];
              console.log(file);
              this.params[response.key].value = response.paths[0];
            },
            handleEdit(index, row,action) {
                    this.dialog.title = "配置:"+row.name;
                    this.payment.id = row.id;
                    this.payment.status = ""+row.status;
                    this.payment.name = row.name;
                    this.params = row.paramsMap;
                    this.dialog.dialogFormVisible = true;
            },
            handleSave() {
              console.log("++++++++++++++++");
              console.log(this.params);
                this.loading = true;
                var that = this;
                that.payment.params = JSON.stringify(this.params);
                that.$api.put('/main_store/payment_config/'+that.payment.id, that.payment, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },
        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



