<template>
  <div class="sidebar">
    <el-menu
      :default-active="activeIndex"
      router
      class="el-menu-vertical-demo"
      :collapse="isCollapse"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >

      <el-menu-item index="/store/storeUser" >
        <i class="el-icon-user-solid"></i>
        <span slot="title">店员管理</span>
      </el-menu-item>
      <el-menu-item index="/store/consumer" >
        <i class="el-icon-s-custom"></i>
        <span slot="title">会员管理</span>
      </el-menu-item>

      <el-menu-item index="/store/orders" >
        <i class="el-icon-s-order"></i>
        <span slot="title">订单管理</span>
      </el-menu-item>

      <el-submenu index="/goods">
        <template slot="title">
          <i class="el-icon-s-shop"></i>
          <span slot="title">店内管理</span>
        </template>
        <el-menu-item index="/store/goods">菜品</el-menu-item>
        <el-menu-item index="/store/goodsTag">菜品标签</el-menu-item>
        <el-menu-item index="/store/tables">餐桌管理</el-menu-item>
      </el-submenu>

      <el-submenu index="/oauth_pay" v-if="configType == 2">
        <template slot="title">
          <i class="el-icon-chat-line-round"></i>
          <span slot="title">点餐配置</span>
        </template>
        <el-menu-item index="/store/payment">支付配置</el-menu-item>
        <el-menu-item index="/store/oauth">授权配置</el-menu-item>
        <el-menu-item index="/store/pageView">主题配置</el-menu-item>
      </el-submenu>


      <el-submenu index="/call">
        <template slot="title">
          <i class="el-icon-message-solid"></i>
          <span slot="title">客人端功能</span>
        </template>
        <el-menu-item index="/store/orderFoodPluginsConfig">功能配置</el-menu-item>

        <el-menu-item index="/store/callTag">呼叫配置</el-menu-item>
        <el-menu-item index="/store/callLog">呼叫记录</el-menu-item>
        <el-menu-item index="/store/chargeItem">充值配置</el-menu-item>
      </el-submenu>
      <el-submenu index="/printer">
        <template slot="title">
          <i class="el-icon-printer"></i>
          <span slot="title">打印管理</span>
        </template>
        <el-menu-item index="/store/printerDataModel">打印模板配置</el-menu-item>
        <el-menu-item index="/store/printer">打印机管理</el-menu-item>
      </el-submenu>
      <!--<el-submenu index="/marketing">-->
        <!--<template slot="title">-->
          <!--<i class="el-icon-s-marketing"></i>-->
          <!--<span slot="title">营销管理</span>-->
        <!--</template>-->
      <!--</el-submenu>-->
      <el-menu-item index="/store/pluginsStore" >
        <i class="el-icon-goods"></i>
        <span slot="title">插件市场</span>
      </el-menu-item>

      <el-menu-item index="/store/setting" >
        <i class="el-icon-setting"></i>
        <span slot="title">系统设置</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Sidebar',
  data () {
    return {
      configType:1,
    }
  },
  computed: {
    ...mapGetters([
      'sidebar'
    ]),
    isCollapse () {
      return !this.sidebar.opened
    },
    activeIndex () {
      const thisRoutPath = this.$route.path
      if (this.$route.name === 'details') {
        return '/tables/details'
      }
      return thisRoutPath
    }
  },
  created(){
    var storage=window.localStorage;
    var user = JSON.parse(storage["user"]);
    this.configType = user.configType;
  },
  methods: {
  }
}
</script>

<style scoped>
  .sidebar {
    background-color: rgb(84, 92, 100);
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 100vh;
    height: 100%;
  }

  .el-menu-vertical-demo {
    min-height: 100vh;
    height: 100%;
  }

  .el-menu-item.is-active,
  .el-submenu.is-active >>> .el-submenu__title {
    background: #484e5c !important;
  }

  .el-submenu .el-menu-item {
    padding-left: 60px !important;
    min-width: auto;
  }
  .el-submenu.is-active .el-menu-item {
    background: #484e5c !important;
  }
  .el-submenu.is-active .el-menu-item.is-active  {
    background: #3a4046 !important;
  }
  .el-submenu .el-menu-item:hover {
    padding-left: 60px !important;
    background: rgb(67, 74, 80) !important;
  }
  .el-menu-item i {
    padding-bottom: 4px;
  }

  .el-submenu__title i {
    padding-bottom: 4px;
  }

  @media all and (max-width: 768px) {
    .el-menu--collapse {
      width: 0;
      overflow: hidden;
    }
  }


</style>



