<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-alert
              title="当分店统一使用配置时，此配置会在分店的客户点餐时生效"
              type="success">
            </el-alert>
            <br/>
            <el-row>
              <el-col :span="6" v-for="(item, key) in tableData" :key="key">
                <el-card style="margin: 5px;height: 350px">
                  <img :src="item.picPath" class="image">
                  <div style="padding: 14px;">
                    <span>{{item.name}}</span>
                    <div class="bottom clearfix">
                      <time class="time">免费
                        (
                        <span v-if="item.status == 4" style="color: green">
                        {{item.statusStr}}
                        </span>
                        <span v-else-if="item.status == 2" style="color: blueviolet">
                        {{item.statusStr}}
                        </span>
                        <span v-else style="color: red">
                        {{item.statusStr}}
                        </span>
                        )
                      </time>
                      <el-button type="text" class="button" v-if="item.status == 4">
                        <el-radio v-model="chooseId" :label="item.id" @change="choosePage(item)"><span v-if="item.id == chooseId">使用中</span><span v-else>未使用</span></el-radio>
                      </el-button>
                      <el-button type="danger" class="button" v-if="item.status == 1 || item.status == 3"  @click="goBuy(item)">
                        购买
                      </el-button>
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                tableData: [],
              chooseId:"",
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/main_store/consumer_view_config', null, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.tableData = res.data.consumerViews;
                    that.chooseId = res.data.usedPageViewId;
                    this.tableData.forEach(function(value,i){
                      value['picPath'] = that.baseImgPath+"/"+value['picPath'];
                    });
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            choosePage(row){
              this.loading = true;
              var that = this;
              that.$api.put('/main_store/consumer_view_config/'+row.id, null, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                    that.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            goBuy(row){
              this.$router.push({path:'/mainStore/pluginsStore',query:{uuid:row.uuid}});
            },
            statusFormat(row,column){
              return row.status == 2?"使用中":"未使用";
            },
        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>

<style>
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 10px;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>



