<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="餐桌"
                prop="orders.tableNumber">
              </el-table-column>
              <el-table-column
                label="菜品"
                prop="goodsName">
              </el-table-column>
              <el-table-column
                label="数量">
                <template slot-scope="scope">
                  <span v-if="scope.row.status == 1">
                    {{scope.row.count - scope.row.makingCount}}
                  </span>
                  <span v-if="scope.row.status == 2">
                    {{scope.row.makingCount - scope.row.makedCount}}
                  </span>
                </template>
              </el-table-column>
              <el-table-column
                label="备注"
                prop="note"
                :show-overflow-tooltip="true"
              >
              </el-table-column>
                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      v-if="scope.row.status == 1"
                      @click="handleMaking(scope.row)">
                        制作
                    </el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDeleteAlert( scope.row)"
                      v-if="scope.row.status == 2"
                    >
                      完成
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>

          <audio autoplay="autoplay"
                 :src="currentSong"
                 style="display: none"
                 id="audio"
          >
          </audio>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                playUrl:"/static/order_tip.mp3",
                audio:null,
                currentSong:null,
                orderGoods:{
                    id:0,
                    name:"",
                    count:0,
                },

                pages:{
                    offset: 0,
                    pageSize: 10,
                    page: 1,
                    total: 0,
                },
              tableData: [],
              tagChecks:[],
              timer:'',
              goods_count:0,
            }
        },
        created(){
            this.initData();

            this.timer = setInterval(this.initData, 30000);//3分种自动更新一次
        },
        methods: {
            initData(){
              //this.loading = true;
              var that = this;
              that.$api.get('/store_user/order_goods/queue', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                      that.tableData = res.data.queue;
                      if(this.goods_count == 0 && that.tableData.length > 0){
                          //语音播报
                          if(this.currentSong == null){
                            this.currentSong = this.playUrl;
                          }else{
                            this.audio = window.document.getElementById('audio');
                            this.audio.play();
                          }
                          this.$notify({
                            title: "新订单",
                            dangerouslyUseHTMLString: true,
                            message: "有新订单来了",
                            duration:5000,
                            position: 'bottom-right',
                            type:"success"
                          });
                      }
                      this.goods_count = that.tableData.length;
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                //this.loading = false;
              });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
          handleMaking(row) {
            var num = row.count - row.makingCount;
            this.$confirm('确定要开始制作 '+ num +'份 '+row.goodsName+' 吗?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.handleSave(row,"making");
            }).catch(() => {

            });
          },
            handleSave(row,action) {
                this.loading = true;
                var that = this;
                that.$api.put('/store_user/order_goods/'+row.id+"/"+action, null, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: '操作成功',
                        type: 'success'
                      });
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                });

                this.loading = false;
            },
            handleDeleteAlert(row) {
              var num = row.makingCount - row.makedCount;
              this.$confirm('确定制作完成了 '+ num +'份 '+row.goodsName+' 吗?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                this.handleSave(row,"maked");;
              }).catch(() => {

              });
            },
        },
        beforeDestroy() {
          clearInterval(this.timer);
        }
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



