<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row>
              <el-button type="primary" @click="handleEdit(0, '','add')">新增打印机</el-button>
            </el-row>
            <br/>
            <el-table
                :data="tableData"
                style="width: 100%"
                v-loading="loading">
              <el-table-column
                label="打印机名字"
                prop="name">
              </el-table-column>

              <el-table-column
                label="状态"
                :formatter="statusFormat">
              </el-table-column>

                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      @click="handleEdit(scope.$index, scope.row,'edit')">
                        配置
                    </el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      v-if="scope.row.status == 1"
                      @click="handleDeleteAlert( scope.row)">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
            </el-table>

            <el-dialog :title="dialog.title" :visible.sync="dialog.dialogFormVisible" :close-on-click-modal="dialog.close" v-loading="loading">
                <el-form :model="printer">
                    <el-form-item label="名称" label-width="100px">
                      <el-input v-model="printer.name" auto-complete="off"></el-input>
                    </el-form-item>

                  <el-form-item label="绑定打印数据模板" label-width="80px">
                    <el-checkbox border  v-for="(item,index) in modelChecks" :key="index"  :label="item.name" v-model="item.check" border></el-checkbox>
                  </el-form-item>

                    <el-form-item label="状态" label-width="100px">
                      <el-switch
                        v-model="printer.status"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        active-text="启用"
                        inactive-text="不启用"
                        active-value="2"
                        inactive-value="1">
                      </el-switch>
                    </el-form-item>

                  <el-form-item label="打印机品牌" label-width="100px">
                    <el-select v-model="printer.chooseuuid" placeholder="请选择" @change="brandChange">
                      <el-option
                        v-for="(item,key) in brands"
                        :key="key"
                        :label="item.name"
                        :value="item.pluginUUID">
                      </el-option>
                    </el-select>
                  </el-form-item>
                    <el-form-item :label="key" label-width="100px" v-for="(item,key) in params" :key="key">
                      <el-input v-model="item.value" auto-complete="off" :placeholder="item.placeholder"></el-input>
                    </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialog.dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="handleSave">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import { baseUrl,baseImgPath } from '../../api/env';
    export default {
        data(){
            return {
                baseUrl,
                baseImgPath,
                loading:false,
                printer:{
                    id:0,
                    name:"",
                    params:"",
                    status:1,
                    pluginUUID:"",
                    chooseuuid:"",
                    data_model_ids:"",
                },
                dialog:{
                    dialogFormVisible:false,
                    title:"新增打印机",
                    close:false,
                },
              tableData: [],
              params:[],
              brands:[],
              modelChecks:[],
            }
        },
        created(){
            this.initData();
        },
        methods: {
            initData(){
              this.loading = true;
              var that = this;
              that.$api.get('/store/printer', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                      that.tableData = res.data.printers;
                  }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                  }
                } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                }
                this.loading = false;
              });
            },

            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pages.page = val;
                this.pages.offset = (val - 1)*this.pages.pageSize;
                this.initData()
            },
            statusFormat(row,column){
              return row.status == 2?"启用中":"未启用";
            },
            handleEdit(index, row,action) {
              this.modelChecks = [];
              this.loading = true;
              var that = this;
              that.$api.get('/store/printer/brand_and_model', that.pages, response => {
                if (response.status >= 200 && response.status < 300) {
                  var res = response.data;
                  if (res.code == 0) {
                      var tags = res.data.printers_data_model;
                      var len = tags.length;
                      for(var i = 0 ; i < len ; i ++){
                        var value = tags[i];
                        this.modelChecks.push({check:false,name:value.name,id:value.id});
                      }



                      that.brands = res.data.printers_brand;
                      if(action == "edit"){
                        this.dialog.title = "配置:"+row.name;
                        this.printer.id = row.id;
                        this.printer.name = row.name;
                        this.printer.status = ""+row.status;
                        this.printer.pluginUUID = row.pluginUUID;
                        this.printer.chooseuuid = row.pluginUUID;

                        this.params = row.paramsMap;

                        if(row.printerDataModels.length > 0 && this.modelChecks.length > 0){//标签选中处理
                          var cids = new Array();
                          for(var i = 0 ; i < row.printerDataModels.length ; i ++){
                            var value = row.printerDataModels[i];
                            cids.push(value.id);
                          }
                          for(var i = 0 ; i < this.modelChecks.length ; i ++){
                            var value = this.modelChecks[i];
                            if(this.in_array(value.id,cids)){
                              this.modelChecks[i].check = true;
                            }
                          }
                        }

                      }else{
                        this.dialog.title = "新增打印机";
                        this.printer.id = 0;
                        this.printer.chooseuuid = this.printer.pluginUUID = that.brands[0].pluginUUID;
                        this.printer.name = that.brands[0].name+"打印机";
                        this.printer.status = "1"
                        this.params = that.brands[0]['paramsMap'];
                      }
                      this.dialog.dialogFormVisible = true;

                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });
                }
                this.loading = false;
              });
            },
            handleDeleteAlert(row) {
              this.$confirm('此操作将删除该打印机吗?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                this.handleDelete(row);
              }).catch(() => {

              });
            },
            handleDelete(row) {
              this.loading = true;
              var that = this;
              that.$api.delete('/store/printer/'+row.id, null, response => {
                if (response.status >= 200 && response.status < 300) {
                  console.log(response.data);
                  var res = response.data;
                  if (res.code == 0) {
                    this.$message({
                      message: '删除成功',
                      type: 'success'
                    });
                    this.initData();
                  }else{
                    this.$message({
                      message: res.msg,
                      type: 'warning'
                    });

                    this.loading = false;
                  }
                } else {
                  this.$message({
                    message: "服务器维护中",
                    type: 'error'
                  });

                  this.loading = false;
                }

              });
            },
            in_array(search,array){
              for(var i in array){
                if(array[i]==search){
                  return true;
                }
              }
              return false;
            },
            brandChange(){
              var that = this;
              if(this.printer.id > 0){//修改
                if(this.printer.pluginUUID == this.printer.chooseuuid){//选择原来的
                    this.tableData.forEach(function(value,i){
                        if(value.pluginUUID == that.printer.chooseuuid){
                          that.params = value['paramsMap'];
                        }
                    });
                }else{
                  this.brands.forEach(function(value,i){
                    if(value.pluginUUID == that.printer.chooseuuid){
                      that.params = value['paramsMap'];
                    }
                  });
                }
              }else{//新增
                this.brands.forEach(function(value,i){
                  if(value.pluginUUID == that.printer.chooseuuid){
                    that.params = value['paramsMap'];
                  }
                });
              }
            },
            handleSave() {
                this.loading = true;
                var that = this;


               that.printer.params = JSON.stringify(this.params);

                that.printer.data_model_ids = "";
                this.modelChecks.forEach(function(value,i){
                  if(value.check == true){
                    that.printer.data_model_ids += value['id']+"#";
                  }
                });
                that.printer.pluginUUID = that.printer.chooseuuid;
                that.$api.post('/store/printer', that.printer, response => {
                  if (response.status >= 200 && response.status < 300) {
                    console.log(response.data);
                    var res = response.data;
                    if (res.code == 0) {
                      this.$message({
                        message: that.dialog.title+'成功',
                        type: 'success'
                      });
                      this.dialog.dialogFormVisible = false;
                      this.initData();
                    }else{
                      this.$message({
                        message: res.msg,
                        type: 'warning'
                      });
                    }
                  } else {
                    this.$message({
                      message: "服务器维护中",
                      type: 'error'
                    });
                  }

                  this.loading = false;
                });

            },
        },
    }
</script>

<style lang="less">
	@import '../../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
        text-align: left;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



