<template>
  <div class="sidebar">
    <el-menu
      :default-active="activeIndex"
      router
      class="el-menu-vertical-demo"
      :collapse="isCollapse"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >

      <!--<el-menu-item index="/superAdmin/mainStore" >-->
        <!--<i class="el-icon-service"></i>-->
        <!--<span slot="title">订单管理</span>-->
      <!--</el-menu-item>-->

      <el-menu-item index="/storeUser/cart" >
        <i class="el-icon-s-order"></i>
        <span slot="title">下单管理</span>
      </el-menu-item>
      <el-menu-item index="/storeUser/orderGoods">
        <i class="el-icon-finished"></i>
        <span slot="title">菜品队列</span>
      </el-menu-item>
      <el-menu-item index="/storeUser/CallLog">
        <i class="el-icon-message-solid"></i>
        <span slot="title">呼叫记录</span>
      </el-menu-item>
      <!--<el-menu-item index="/setting" >-->
        <!--<i class="el-icon-setting"></i>-->
        <!--<span slot="title">系统设置</span>-->
      <!--</el-menu-item>-->
    </el-menu>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Sidebar',
  data () {
    return {
      orderGoodsType:"1",
    }
  },
  computed: {
    ...mapGetters([
      'sidebar'
    ]),
    isCollapse () {
      return !this.sidebar.opened
    },
    activeIndex () {
      const thisRoutPath = this.$route.path
      if (this.$route.name === 'details') {
        return '/tables/details'
      }
      return thisRoutPath
    }
  },
  created(){
    this.initData();
  },
  methods: {
    initData(){
      this.loading = true;
      var that = this;
      that.$api.get('/store_user/setting', null, response => {
        if (response.status >= 200 && response.status < 300) {
          var res = response.data;
          if (res.code == 0) {
            that.orderGoodsType = ""+res.data.setting.orderGoodsType;
          }else{
            this.$message({
              message: res.msg,
              type: 'warning'
            });
          }
        } else {
          this.$message({
            message: "服务器维护中",
            type: 'error'
          });
        }
        this.loading = false;
      });
    },
  }
}
</script>

<style scoped>
  .sidebar {
    background-color: rgb(84, 92, 100);
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 100vh;
    height: 100%;
  }

  .el-menu-vertical-demo {
    min-height: 100vh;
    height: 100%;
  }

  .el-menu-item.is-active,
  .el-submenu.is-active >>> .el-submenu__title {
    background: #484e5c !important;
  }

  .el-submenu .el-menu-item {
    padding-left: 60px !important;
    min-width: auto;
  }
  .el-submenu.is-active .el-menu-item {
    background: #484e5c !important;
  }
  .el-submenu.is-active .el-menu-item.is-active  {
    background: #3a4046 !important;
  }
  .el-submenu .el-menu-item:hover {
    padding-left: 60px !important;
    background: rgb(67, 74, 80) !important;
  }
  .el-menu-item i {
    padding-bottom: 4px;
  }

  .el-submenu__title i {
    padding-bottom: 4px;
  }

  @media all and (max-width: 768px) {
    .el-menu--collapse {
      width: 0;
      overflow: hidden;
    }
  }


</style>



